<template>
  <div id="layout-setting-component">
      <div class="block-layout-control">
          <div class="control-layout">
            <div>Charge: </div>
            <el-slider v-model="layoutParam.chargeStrength" :min=-500 :max=500 :step=10></el-slider>
          </div>

          <div class="control-layout"> 
          <div>Link Length: </div>
          <el-slider v-model="layoutParam.linkLength" :max=200 :step=5></el-slider>    
          </div>

          <div class="control-layout">
          <div>Edge Mode:</div>               
          <el-switch
            style="display: block"
            v-model="layoutParam.edgeMode"               
            active-text="line"
            inactive-text="curve">
          </el-switch>
          </div>
          
          <div class="control-layout">
          <div>Label:</div>               
          <el-switch
            style="display: block"
            v-model="labelDisplay"               
            active-text="true"
            inactive-text="false">
          </el-switch>
          </div>

      </div>
      <div class="reset-fault-layout-settings">
        <el-button size="mini" type="primary" @click="onReset">Reset</el-button>
      </div>
  </div>
</template>

<script>
  import $ from 'jquery'
  import bus from '../eventbus.js' // .
  export default {    
    data: function(){
      return {
        layoutParam:{
          linkLength: 80, // .
          chargeStrength: -200, // .
          edgeMode: true, // :curve- false, line- true.          
          limitBox: false, // .
          nodeColor:"#FF8700", // .
          baseSize: 10, // .
          labelDisplay: true //
        },
        labelDisplay: true, // ,css.
        
      }
    },
    props:[
     'busEventName' //.
    ],
    watch:{
      layoutParam:{ //
        handler(curVal, oldVal){
           
           let this_ = this;
           curVal.labelDisplay = this_.labelDisplay;
           let newObj = JSON.parse(JSON.stringify(curVal)); //.
           // ,.
           if(newObj.edgeMode == true){ // "line"
             newObj.edgeMode = "line"; //
           }
           else{ //"curve"
             newObj.edgeMode = "curve"; //
           }
           bus.$emit(this_.busEventName, [true, newObj]); //true: , ,,.
           // bus.$emit(this_.busEventName, "curVal");
        },
        deep:true
      },
      labelDisplay: function (curVal, oldVal){
        let this_ = this;
        bus.$emit(this_.busEventName, [false, curVal]); //false:.
      }
    },
    methods:{
      onReset(){ // .         
        this.layoutParam.chargeStrength = -200;
        this.layoutParam.linkLength = 80;
        this.layoutParam.edgeMode = true;
        this.labelDisplay = true;
      },      
    },
    created(){
      let this_ = this;
    },
    mounted(){
      let this_ = this;
      
    }    
  }
</script>

<style>
 #layout-setting-component .block-layout-control{
     border-style: solid;
     border-color:#E0E0E0;
     border-top-width: 1px;
     border-right-width: 0px;
     border-bottom-width: 0px;
     border-left-width: 0px;
     margin: 0px 0px 2px 0px;
     padding: 0px 0px 0px 5px;
  }
  #layout-setting-component .reset-fault-layout-settings{     
     float: right;
     margin-right: 5px;
  }
  #layout-setting-component .control-layout{
    margin:0px 0px 0px 0px;
  }
  #layout-setting-component .control-layout div{
    display:inline-block;    
  }
  
  .reset-fault-layout-settings .el-button--mini{
    padding-top: 7px;
    padding-right: 4px;
    padding-bottom: 7px;
    padding-left: 4px;
    margin: 0px 2px 0px 0px;
  }
</style>



