                   let conditionsList = this_.dynamicConditionExploreObj[keysList[i]]; // [{id:x, tag:x}, ...]
                   let finalMatchNodes = this_.matchNodesforConditions(conditionsList); // .             
                   this_.matchedNodesObj[keysList[i]] = finalMatchNodes; // {id0:[], id1:[], ...}
                   this_.nodeVsColorObj[keysList[i]] = this_.highLightColorList[i]; // : {id0: color0, id1: color1, id2: color2, ...}
                   // this_.highLightNodesOnMatchedNodesMultiple(finalMatchNodes, this_.highLightColorList[i]); // .
                   let matchingNodeList = this_.getMatchingData(this_.matchedNodesObj, this_.nodeVsColorObj);            
