<template>
  <div :id="nodeinfobox"><!--: ,-->
    <div id="node-info-box">
      <el-table
      :data="tableData"
      border
      :height="height"
      :row-class-name="tableRowClassName"
      :cell-class-name="tableCellClassName"
      empty-text="null"    
      style="width: 100%"
      @cell-mouse-enter="handleMouseEnter"
      @cell-mouse-leave="handleMouseLeave"
      @row-click="getRowDetail"
      ><!---->
      <el-table-column
        prop="key"       
        label="key"
        :width="widthKey"
        show-overflow-tooltip 
      >
      </el-table-column>
      <el-table-column
        prop="value"      
        label="value"               
        :width="widthValue"      
        filter-placement>
      </el-table-column>
      </el-table>
    </div>
     
     <div class="nodeinfo-see-more" style="display: none;"></div> <!--,-->     
     
  </div>
</template>

<script>
  import {vueFlaskRouterConfig} from '../vueFlaskRouterConfig'
  import bus from '../eventbus.js' // .
  import $ from 'jquery'
  import axios from 'axios' // AJAX  
  import {jBox} from "../../static/js/jBox.js"  
  import "../../static/js/jquery.contextMenu.js"
  import "../../static/js/jquery.ui.position.js"

  export default {
    data() {
      return {
        tableData: null,
        widthKey: 0,
        widthValue:150,
        height:200,

        xOffset: 0,  // .
        yOffset: 40, 

        unitWidth: 10, // .115px.

        overFlowLen: 18, // ,18,,.
        
        ableClickClassName: "clickSeeMore", // .
        
        nodeIdName:{}, // {id:x, name:x}.
        flagOfTableDataChange: false, // .
        seeMoreKeyValObj: {}, // See More, e.g. {publications:"123;345;223"}        
        seeMoreJbox:null, // .
      }
    },
    props:[   
      "nodeinfobox", //divid,.    
      "sendnodeinfobusevent", // .
      "sendsvgidtocomp", //svgid,svg,SVG.
      "isopenchecknodedetailflag", //:[true][false],:()
      
      "targetselector", // targetselector targetoffset
      "targetoffset"
    ],     
    
    created(){
      console.log("nodeInfo.vue created");
      let this_ = this;          
    },
    watch:{
      tableData: function(curVal, oldVal){
        let this_ = this;
        this_.flagOfTableDataChange = true;
      }
    },
    methods:{
      insertSeeMore(){ // See More.
        let this_ = this;
        let keyList = Object.keys(this_.seeMoreKeyValObj); // [x, x, ...]
        // console.log("this_.seeMoreKeyValObj");console.log(this_.seeMoreKeyValObj);
        for(let i=0; i<keyList.length; i++){
          let className = keyList[i]; // ,calss name.
          let html = "<p class='cell-see-more'>See More...</p>";
          $("#" + this_.nodeinfobox + " #node-info-box ." + className + " .value .cell").html(html); //see more,.
        }        
      },
      orderForArray(yearArr, idArr){ //.
         let orderIdArr = [];
         let orderYear = [];

         let newYearArr = yearArr.slice();

         for(let i=0; i<newYearArr.length; i++){
            let maxIndex = this.indexOfMax(newYearArr);
            let maxVal = newYearArr[maxIndex];
            newYearArr[maxIndex] = 0;

            orderYear.push(maxVal);
            orderIdArr.push(idArr[maxIndex]);
         }
         return {orderIdArr: orderIdArr, orderYear: orderYear};
      },
      indexOfMax(arr) {
          if (arr.length === 0) {
              return -1;
          }

          var max = arr[0];
          var maxIndex = 0;

          for (var i = 1; i < arr.length; i++) {
              if (arr[i] > max) {
                  maxIndex = i;
                  max = arr[i];
              }
          }

          return maxIndex;
      },
      initSeeMore(){
        let coll = $(".nodeinfo-see-more .collapsible"); //nodeinfo-see-more .
        for(let i=0; i<coll.length; i++){
          coll[i].addEventListener("click", function() {
            this.classList.toggle("active");
            let content = this.nextElementSibling;
            if (content.style.display === "block") {
              content.style.display = "none";
            } else {
              content.style.display = "block";
            }
          });
        }
      },
      sprintf(){  // .
        let args = arguments,
        string = args[0],
        i = 1;
        return string.replace(/%((%)|s|d)/g, function (m) {
            // m is the matched format, e.g. %s, %d
            let val = null;
            if (m[2]) {
                val = m[2];
            } else {
                val = args[i];
                // A switch statement so that the formatter can be extended. Default is %s
                switch (m) {
                    case '%d':
                        val = parseFloat(val);
                        if (isNaN(val)) {
                            val = 0;
                        }
                        break;
                }
                i++;
            }
            return val;
        });
      },
      tableCellClassName({row, column, rowIndex, columnIndex}){        
        return column.label;
      },
      tableRowClassName({row, rowIndex}){
        let this_ = this;
        // console.log("tableRowClassName");
        let fieldName = row.key;
        let className  = this_.ableClickClassName + " " + fieldName; // .
        if(this_.$store.state.infoSearchView.dbtables.indexOf(fieldName) != -1){ // .
           return className;
        }
        else{
          return fieldName;
        }        
      }, 
      mousePosition(ev){ 
          ev = ev || window.event; 
          if(ev.pageX || ev.pageY){ 
              return {x:ev.pageX, y:ev.pageY}; 
          }
          return { 
              x:ev.clientX + document.body.scrollLeft - document.body.clientLeft, 
              y:ev.clientY + document.body.scrollTop - document.body.clientTop 
          }; 
      },     
      handleMouseEnter(row, column, cell, e){
        let this_ = this;
        let text = "of";
        let innerHTML = row.value;
        let field = row.key; // 
        // console.log("row");console.log(row);  
        // console.log("column");console.log(column.label);
        // console.log("cell");console.log(cell);
        // console.log("event");console.log(e);         
        let isValue = column.label;        
        // attrValue:"XXX, VVV, Nnnn", // text
        // curNodeId:"13424", // id
        // curNodeArr:field // ()
        let lenInnerHTML = innerHTML.length; // .
        if(isValue == "value" && lenInnerHTML > this_.overFlowLen){ // lenInnerHTML > 18.
            let mousePos = this_.mousePosition(e);
            let  xOffset = this_.xOffset;
            let  yOffset = this_.yOffset;
            $(".detail-info-box-mouse-over").css("display","block").css("position","absolute")
            .css("top",(mousePos.y - yOffset) + "px")
            .css("left",(mousePos.x + xOffset) + "px");
            if(this_.$store.state.infoSearchView.dbtables.indexOf(field) != -1){ // 
               let newHtml = "<p>%s</p>";
               let newInnerHTML = this_.sprintf(newHtml, "Click to see more!");
               $(".detail-info-box-mouse-over").html(newInnerHTML);
            }
            else{ // .
               $(".detail-info-box-mouse-over").html(innerHTML);
            }
                
        }
        if(isValue == "value" && this_.$store.state.infoSearchView.dbtables.indexOf(field) != -1 && lenInnerHTML <=this_.overFlowLen){ // 
          let mousePos = this_.mousePosition(e);
          let  xOffset = this_.xOffset;
          let  yOffset = this_.yOffset;
          $(".detail-info-box-mouse-over").css("display","block").css("position","absolute")
            .css("top",(mousePos.y - yOffset) + "px")
            .css("left",(mousePos.x + xOffset) + "px");
          let newHtml = "<p>%s</p>";
          let newInnerHTML = this_.sprintf(newHtml, "Click to see more!");
          $(".detail-info-box-mouse-over").html(newInnerHTML);
        }
      },
      handleMouseLeave(row, column, cell, e){
        let this_ = this;
        let isValue = column.label;
        if(isValue == "value"){
          $(".detail-info-box-mouse-over").css("display","none"); // class=detail-info-box-mouse-overdivmainView.vue..
        }
      },
      getRowDetail(row, event, column){       
        let this_ = this;
        let key = row.key;
        let value = null;
        if(this_.$store.state.infoSearchView.dbtables.indexOf(key) != -1){ // .
           value = this_.seeMoreKeyValObj[key];
        }
        else{
           value = row.value;
        }  
        let keysType = this_.$store.state.fields.fieldsType; // .{:,...}
        if(keysType[key] == "text" && this_.$store.state.infoSearchView.dbtables.indexOf(key) == -1){ // textseemore,.
        // if(key == 'id') {
         // text,.
          bus.$emit('sendNodeId', value); //sendNodeId,infoSearch.vue.
        }    
        
        if(this_.$store.state.infoSearchView.dbtables.indexOf(key) != -1){ // seemore. publications.
          // todo:,.          
          let dbName = this_.$store.state.selection.selectiondb; // .
          let tableName = key;
          let param = { "dbName": dbName, // DB name
                        "value": value, // value="id1;id2;id3;id4"
                        "tableName": tableName}; // tableName="publications",.
          
          axios.post(vueFlaskRouterConfig.mainViewNodeDetailsSeeMore, {
              param: JSON.stringify(param)
          })
          .then((res) => { 
              let data = res.data;  // ,id.
              if(tableName == "publications"){ // todo: publications,..
                // this_.jboxTitle = "Publication";
                let allIdsList = Object.keys(data); // [id1, id2, ...], {id:{}, ...}
                
                let yearList = []; // [2016, 2018, ...] int.
                for(let i=0; i<allIdsList.length; i++){
                  let id = allIdsList[i];
                  let eachObj = data[id];
                  yearList.push(parseInt(eachObj.year));
                }                
                let orderObj = this_.orderForArray(yearList, allIdsList); // {orderIdArr: orderIdArr, orderYear: orderYear}              
                let orderId = orderObj.orderIdArr;                
                let totalNum = allIdsList.length; // .
                let tempHtml = "<div class='author-number'><span>Author: %s</span><span>&nbsp&nbsp&nbsp&nbsp&nbsp&nbspnumber: %s</span></div>"; 
                let allHtml = this_.sprintf(tempHtml, this_.nodeIdName.name, String(totalNum));     
                for(let i=0; i<orderId.length; i++){
                  let id = orderId[i];
                  let obj = data[id];
                  let title = obj.title;
                  let authors = obj.authors;
                  let public_venue = obj.public_venue;
                  let year = obj.year;
                  let abstract = obj.abstract;
                  let html = '<div class="each-one"><h5 class="content">%s. %s</h5><p class="content"><em id="author">%s</em></p><p class="content"><strong id="venue">%s(%s)</strong></p><span class="collapsible"><img src="data:image/gif;base64,"><span class="collapsible-icon-name">Abstract</span></span><div class="collapsible-content"><div class="abstract"><div class="abstract-content">%s</div></div></div></div>';
                  let newHtml = this_.sprintf(html, String(i+1), title, authors, public_venue, year, abstract);
                  allHtml += newHtml;
                }
                $(".nodeinfo-see-more").html(allHtml); // updated.,initSeeMore.
                this_.initSeeMore();
              }           
              
          })
          .catch((error) => {            
            console.error(error);
          });
        }
      },
      getMaxOfArray(numArray) {
        return Math.max.apply(null, numArray);
      },
    },
    mounted(){       
      let this_ = this; // VueComponent
      console.log("nodeInfo.vue mounted");
      bus.$on(this_.sendnodeinfobusevent, function (data) {  // :bus.$on()mounted(){},,,function(){}.            
            // function(){},thisbus. ()=>{},thisvuecomponent.             
            this_.tableData = data; //,.
            console.log("data infocomponent");console.log(data);
            let fieldList = [];
            let nodeIdName = {}; // {id:x, name:x}, See More.
            let seeMoreKeyValObj = {}; // e.g. {publications:"123;345;223"}
            for(let i=0; i<data.length; i++){
              let obj = data[i]; // {"key": "id", "value": "12345"}
              let key = obj.key; // .
              fieldList.push(key.length);

              if(key == "name"){
                 nodeIdName.name = obj.value;
              }
              if(key == "id"){
                nodeIdName.id = obj.value;
              }

              if(this_.$store.state.infoSearchView.dbtables.indexOf(key) != -1){ // .
                seeMoreKeyValObj[key] = obj.value; // {publications:"123;345;223"}
              }
            }
            // console.log("seeMoreKeyValObj");console.log(seeMoreKeyValObj);
            let maxLenField = this_.getMaxOfArray(fieldList); // .
            this_.widthKey = maxLenField * this_.unitWidth;
            this_.nodeIdName = nodeIdName;
            this_.seeMoreKeyValObj = seeMoreKeyValObj;
      });

    },
    updated(){
      console.log("nodeInfoView updated");
      let this_ = this;      
      if(this_.tableData.length > 0 && this_.flagOfTableDataChange){ //.
          this_.flagOfTableDataChange = false;
          this_.insertSeeMore(); // ,cell"See More...".        
          this_.seeMoreJbox = new jBox('Modal', {                  
                  addClass: this_.sendnodeinfobusevent + "jBoxSeeMoreOfNode",  // ,!
                  attach: "#" + this_.nodeinfobox  + " ." + this_.ableClickClassName, //#nodeinfobox,.
                  width: 400,              // Maximal width 
                  maxHeight: 800,              
                  title: "Publications",
                  overlay: false,
                  zIndex: 1005, // fixme:jboxzIndex.
                  createOnInit: true,
                  content: $(".nodeinfo-see-more"),  //.nodeinfo-see-more.
                  draggable: true,
                  repositionOnOpen: false,
                  repositionOnContent: true,
                  // target: $(this_.targetselector), //targetselector targetoffset
                  // offset: this_.targetoffset,//{x: 95, y: 145},                  
                  position:this_.$store.state.jboxPositionSet.topRightSide,
                  // ,!
                  onOpen: function(){
                      // this_.seeMoreJbox.position({
                      //   target: $(this_.targetselector)
                      //   // ,
                      //   // offset: this_.targetoffset        
                      // });
                      let css = {
                        "stroke-dasharray":0,
                        "stroke": "#fff",
                        "stroke-width": this_.$store.state.mainViewGraph.highlightColorSheet.strokeWidthNull
                      };
                      $("#" + this_.sendsvgidtocomp + " .nodecircle").css(css); //                       

                      // .
                      let cssNode = {
                        "stroke-dasharray":0,
                        "stroke": this_.$store.state.mainViewGraph.highlightColorSheet.clickCheckNodeInfo,
                        "stroke-width": this_.$store.state.mainViewGraph.highlightColorSheet.strokeWidth
                      };
                      let nodeId = this_.$store.state.globalFilteredSubgraphView.clickNode2GetId;
                      $("#" + this_.sendsvgidtocomp + " ." + nodeId).css(cssNode); // ,.
                  },
                  onCloseComplete: function(){ // todo: : onCloseComplete.see more,mainViewjquerycssdiaplay == block none.
                    
                     // this_.$store.state.nodeInfoView.isOpenSeeMoreFlag = false; // .
                     console.log("this_.isopenchecknodedetailflag[0]");console.log(this_.isopenchecknodedetailflag[0]);
                     if(this_.isopenchecknodedetailflag[0]){ // .                        
                         let css = {
                            "stroke-dasharray":0,
                            "stroke": "#fff",
                            "stroke-width": this_.$store.state.mainViewGraph.highlightColorSheet.strokeWidthNull
                          };
                          $("#" + this_.sendsvgidtocomp + " .nodecircle").css(css); // .                        

                          // .
                          let cssNode = {
                              "stroke-dasharray":0,
                              "stroke": this_.$store.state.mainViewGraph.highlightColorSheet.clickCheckNodeInfo,
                              "stroke-width": this_.$store.state.mainViewGraph.highlightColorSheet.strokeWidth
                          };
                          let nodeId = this_.$store.state.globalFilteredSubgraphView.clickNode2GetId;
                          $("#" + this_.$store.state.globalFilteredSubgraphView.svgId + " ." + nodeId).css(cssNode); // ,. // ,.
                     }
                     else{ // .
                        let css = {
                          "stroke-dasharray":0,
                          "stroke": "#fff",
                          "stroke-width": this_.$store.state.mainViewGraph.highlightColorSheet.strokeWidthNull
                        };
                        $("#" + this_.sendsvgidtocomp + " .nodecircle").css(css); // .

                     }
                  }
          });
          if($("." + this_.sendnodeinfobusevent + "jBoxSeeMoreOfNode").length > 1){ // id=jBoxSeeMoreOfNodeIddiv.
            $("." + this_.sendnodeinfobusevent + "jBoxSeeMoreOfNode").first().remove(); // .first()
          }   
                
      }      
      
    },
    beforeDestroy () {
      let this_ = this;
      bus.$off(this_.sendnodeinfobusevent);  // bus.on(),bus.$off(),.
       
    },
  }
</script>

<style>
    /*@import "../../static/css/jBox.css";
    @import "../../static/css/jquery.contextMenu.css";*/
  .highlight {
    background-color: yellow;
  }  
  .cell-see-more{
    color:#069;
  }
  #node-info-box .el-table .cell, #node-info-box .el-table th div{
    padding-right: 10px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .nodeinfo-see-more {
    /*display: none;*/
    max-height: 800px; 
    max-width: 700px;
    padding:2px 2px 2px 4px;
    overflow: scroll;
  }
  
/*  */
.nodeinfo-see-more .abstract {
  max-height: 217px;
  max-width: 489px;
  overflow: scroll;
  padding: 0px 0px 0px 5px;
  font: normal normal normal normal 12px / 20px Lato, sans-serif;
  /*border-style: solid;
  border-color:#E0E0E0;
  border-top-width: 1px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-left-width: 1px;*/
  margin: 5px 1px 5px 1px;
}
.nodeinfo-see-more .author-number{
  margin:2px 0px 8px 0px;
  border-style: solid;
  border-color:#E0E0E0;
  border-top-width: 0px;
  border-right-width: 0px;
  border-bottom-width: 1px;
  border-left-width: 0px;
}
.nodeinfo-see-more .author-number span{
  width:100%;
  border-spacing: 2px 2px;
  font: normal normal normal normal 14px;
  
}
.nodeinfo-see-more #author{
    color: #069;
    border-spacing: 2px 2px;
    font: italic normal 300 normal 12px / 20px Lato, sans-serif;
}
.nodeinfo-see-more #venue{
    border-spacing: 2px 2px;
    font: normal normal normal normal 12px / 20px Lato, sans-serif;
}
.nodeinfo-see-more h5{
   color: #069;
}
.nodeinfo-see-more .content{
  width: 489px;
  margin: 1px 1px 1px 1px;

}
#info-paper-box{
  max-height: 700px;
  max-width: 500px;
  overflow: scroll;
}

.nodeinfo-see-more .collapsible {
  color: #444;
  cursor: pointer;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}

.nodeinfo-see-more .active .collapsible-icon-name, .nodeinfo-see-more .collapsible .collapsible-icon-name:hover { /*.collapsible */
  background-color: #069;
}


.nodeinfo-see-more .collapsible-content {
  display: none;
  overflow: hidden;
  background-color: #f1f1f1;
}
.nodeinfo-see-more .each-one{
 border-style: solid;
 border-color:#E0E0E0;
 border-top-width: 0px;
 border-right-width: 0px;
 border-bottom-width: 1px;
 border-left-width: 0px;
}
.cell-see-more {
   -webkit-margin-before: 0em;
    -webkit-margin-after: 0em;
    -webkit-margin-start: 0px;
    -webkit-margin-end: 0px;
}
</style>


