              // this_.isHighLightCurrentRow = false; //.
              this_.hightLightCurRow(row, false); //.
              this_.jBoxInstance.seeMoreJbox.close(); //,seemore
            }
            else{ //,,id,.
              let css_ = {
                "stroke-dasharray":0,
                "stroke": "#fff",
                "stroke-width": 0
              };
              $("#" + this_.$store.state.resultsSearchView.svgId + " ." + this_.tableRowClickNodeList[0]).css(css_); //.
              // this_.isHighLightCurrentRow = false; //.
              this_.hightLightCurRow(this_.selectedRowList[0], false);
              this_.tableRowClickNodeList.splice(0, 1); //nodeId,,,,.
              this_.selectedRowList.splice(0, 1);
              this_.tableRowClickNodeList.push(nodeId);
              this_.selectedRowList.push(row);
              let css = {
               "stroke-dasharray":0,
               "stroke": "black",
               "stroke-width": 3
              };
              $("#" + this_.$store.state.resultsSearchView.svgId + " ." + nodeId).css(css); //.
               // this_.isHighLightCurrentRow = true; //.
               this_.hightLightCurRow(row, true); //.
               this_.localCurrentRow(row, index); //.
            }
          }
          else{ //,,id,.
             this_.tableRowClickNodeList.push(nodeId);
             this_.selectedRowList.push(row);
             let css = {
               "stroke-dasharray":0,
               "stroke": "black",
               "stroke-width": 3
              };
              $("#" + this_.$store.state.resultsSearchView.svgId + " ." + nodeId).css(css); //.
               // this_.isHighLightCurrentRow = true; //.
