<template>
<div id="load-db-view"> 
  <el-form :inline="true" class="db-select-form">
          <el-form-item v-loading="loadingDbFlag" label="">  <!---->          
            <el-select 
            v-model="selectiondb" 
            filterable
            @visible-change="dropDownBoxVisible" 
            placeholder="Select Dataset">
                <el-option
                    v-for="(item, index) in dboptions"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
          </el-form-item>
          
      </el-form>
  </div> 
</template>

<script>
  import axios from 'axios' // AJAX
  import * as d3 from '../../static/js/d3.v4.min.js'
  import $ from 'jquery'
  import {vueFlaskRouterConfig, searchFlag} from '../vueFlaskRouterConfig'
  import bus from '../eventbus.js' // .

  export default {
	data(){
	return {
	    loadingDbFlag: false, // .
	     // loadingFlag: false, // + .
	     selectiondb: '', // v-model.
	     dboptions:[], // .
	     dbLoadedFlag:{}, // .,DOI.
	     histClickFlag: [false], // .[true],.
       stopwordsObj: null, //.
	 }
	},
	watch:{
	  selectiondb: function(curVal, oldVal){ // /
        let this_ = this;
        this_.$store.state.selection.selectiondb = curVal; // .
        this_.$store.state.mainViewGraph.d3EventTransform = {};
        bus.$emit("sendClearNodeHighLighting", true); //.
        bus.$emit("sendRankingDefaultMethod", curVal); // ranking method API.
        if(this_.stopwordsObj){ // null
          let stopWords = this_.stopwordsObj; //.
          let arrCommonStopWords = stopWords.commonStopWords; // .              
          let obj = stopWords.customStopWords; // ,{dbname:[], ...}        
          let arrCustomStopWords = obj[curVal]; // .         
          this_.$store.state.mainViewGraph.stopWords = arrCommonStopWords.concat(arrCustomStopWords)// .         
        }
        else{ // null,
          let path = vueFlaskRouterConfig.infoSearchForDbSelection;
          axios.get(path) // getpost.
          .then((res) => {
              // this_.loadingDbFlag = false;                
              let data = res.data; // .            
              this_.dboptions = data.dbname; // [{}, ...] 
              // a.slice(0,-3) 
              for(let i=0; i<this_.dboptions.length; i++){
                 let dbName = this_.dboptions[i].label;
                 let key_ = dbName.slice(0,-3); // .db,.
                 this_.dbLoadedFlag[key_] = false;  // {dbname1: false, ...} .
              } 
              
              this_.$store.state.attrNode.discardfield = data.discardfield; // ,. [x, x, ...]
              this_.$store.state.attrNode.fullCompatibilityAtrr = data.fullCompatibilityAttr; // [x, x, ...] mainView,,,institution.
              // console.log("this_.$store.state.attrNode.discardfield");console.log(this_.$store.state.attrNode.discardfield);
              // console.log("this_.$store.state.attrNode.fullCompatibilityAtrr");console.log(this_.$store.state.attrNode.fullCompatibilityAtrr);
              this_.stopwordsObj = data.stopword; //,json. 
              let stopWords = this_.stopwordsObj; //.
              let arrCommonStopWords = stopWords.commonStopWords; // .              
              let obj = stopWords.customStopWords; // ,{dbname:[], ...}        
              let arrCustomStopWords = obj[curVal]; // .         
              this_.$store.state.mainViewGraph.stopWords = arrCommonStopWords.concat(arrCustomStopWords)// .                     
                              
            })
          .catch((error) => {            
            console.error(error);
          });
        }             
        
        let histClickFlag = this_.histClickFlag[0]; // .
        
        if(!histClickFlag){ // , .
            if(curVal != "" && oldVal != ""){ // .              
              let labelsClear = true; // 
              bus.$emit("focusSetClear", labelsClear);  // focusSetView.vue,.
              let settings = this_.onReset();  // .
              bus.$emit("Settings", settings); // settingsSettings.
            }
        }
        else{ // ,.
          this_.histClickFlag.pop();
          this_.histClickFlag.push(false);
          console.log("this_.histClickFlag.push(false)");          
        }
        $("svg#mainsvg > *").remove(); // svg.,svg,jquery(vue)svg#mainsvg,.      
        bus.$emit("sendClearAttriExplorState", true); // ,.

        // this_.$store.state.selection.selectiondb = curVal; // store.js.
        if(curVal != ""){ //.
          // this_.loadingFlag = true; //.
          bus.$emit("sendLoadingFlagForFeildKeyWord", true); // + .         
          let path = vueFlaskRouterConfig.infoSearchForDbFields + "/" + curVal; // URL.          
          axios.get(path)
          .then((res) => {
                  // fixme: ,node +  + nodeedge.
                  // this_.loadingFlag = false; // .
                  bus.$emit("sendLoadingFlagForFeildKeyWord", false); // + .
                  for(let key in this_.dbLoadedFlag){
                      let tempName = key + ".db"; //.
                      if(tempName == curVal){ //.
                        this_.dbLoadedFlag[key] = true; //.
                        // API,N.
                        if(this_.$store.state.isUsingRankMeasures){ // .                          
                          let requestNodeRankTable = vueFlaskRouterConfig.selctionFocusNodesByNodeMeasures + "/" + this_.$store.state.selection.selectiondb + "/API" + "/" + this_.$store.state.nodeRankTable.topN;
                          axios.get(requestNodeRankTable)
                          .then((res) => {
                            let nodeRankTable = res.data; //.
                            // this_.$store.state.globalGraphView.globalGraphLayoutData = globalGraphLayoutData; // .
                            bus.$emit("sendRankTableNodeMeasure", nodeRankTable); //.

                          })
                          .catch((error) => {            
                            console.error(error);
                          });
                        }
                      }
                      else{
                        this_.dbLoadedFlag[key] = false;
                      }
                  } 
                  // console.log("this_.dbLoadedFlag");console.log(this_.dbLoadedFlag);
                  
                  let data = res.data; 
                  // let dbtables = data.dbtables; // ,"see more"
                  this_.$store.state.infoSearchView.dbtables = data.dbtables; // ,"see more"
                  this_.$store.state.infoSearchView.numNodes = data.numNodes; // 
                   
                  let fields = data.fields; // .[{label:, value:}, ...]
                  
                  bus.$emit("sendLoadedInitFieldKeyWord", fields); //,+ .

                  // this_.optionsfield.splice(0,this_.optionsfield.length);  // . :.
                  // let allFields = {label:"All", value:"All"};
                  // this_.optionsfield.push(allFields);
                  // for(let i=0; i<fields.length; i++){
                  //   this_.optionsfield.push(fields[i]);
                  // }                 
                  // this_.selectionfield = "All"; // all.
                  // this_.allFieldData.splice(0, this_.allFieldData.length);  // . 
                  // this_.keyWord = "";
                  
                  let fieldsType = data.fieldstype; // .{:,...}
                  this_.$store.state.fields.fieldsType = fieldsType; // store.js.
                  // console.log("this_.$store.state.fields.fieldsType");console.log(fieldsType); 
                  bus.$emit("sendFocusAttriType", fieldsType); // focusAttributesSelection.vue.                  
                  bus.$emit("dbLoadedState", true);
                 
          })
          .catch((error) => {            
            console.error(error);
          });
          // .
          if(this_.$store.state.isShowGlobalGraphFlag){
            let requestGlobalGraphPath = vueFlaskRouterConfig.globalGraphLayout + "/" + curVal;
            axios.get(requestGlobalGraphPath)
            .then((res) => {
              let globalGraphLayoutData = res.data; //.
              this_.$store.state.globalGraphView.globalGraphLayoutData = globalGraphLayoutData; // .
              bus.$emit("sendGlobalGraphLayoutData", true); //.

            })
            .catch((error) => {            
              console.error(error);
            });
          }
          // if(this_.$store.state.isUsingRankMeasures){ // .
          //   let requestNodeRankTable = vueFlaskRouterConfig.selctionFocusNodesByNodeMeasures + "/" + this_.$store.state.selection.selectiondb + "/default" + "/" + this_.$store.state.nodeRankTable.topN;
          //   axios.get(requestNodeRankTable)
          //   .then((res) => {
          //     let nodeRankTable = res.data; //.
          //     // this_.$store.state.globalGraphView.globalGraphLayoutData = globalGraphLayoutData; // .
          //     bus.$emit("sendRankTableNodeMeasure", nodeRankTable); //.

          //   })
          //   .catch((error) => {            
          //     console.error(error);
          //   });
          // }          

        }       

        let nullObj = {numnodes:0, numedges:0};
        bus.$emit("sendDoiGraphInfo", nullObj);
      
    }
	},
	methods:{
		 onReset(){
          let settings = {  // Settings.
            // scaleOfSingleFocus: 20,  // scale Of Single Focus,.  
            // explandneighbors: 10, // .  
            // diffFactor: 0.8,  // .  
            // UIFactor: 0.9,  // UI_Factor,,API_factor = 1 - UI_factor.
            // APIFactor: 0.1,  // API_Factor
            // isEdgeAttri: true, // DOI
            // probRestart: 0.3,  // RWR.  
            // weightAttrNode: 1, // -.  
            scaleOfSingleFocus: 30,  // scale Of Single Focus,.  
            explandneighbors: 5, // .  
            diffFactor: 0.85,  // .  
            UIFactor: 0.9,  // UI_Factor,,API_factor = 1 - UI_factor.
            APIFactor:0.1,  // API_Factor
            isEdgeAttri: true, // DOI
            probRestart: 0.7,  // RWR.  
            weightAttrNode: 1, // -. 
          };
          return settings;
     },
	   dropDownBoxVisible(flag){
	        let this_ = this;	        
	        if(flag && this_.dboptions.length == 0){ //,,.
	          this_.loadDbs();
	        }
     },
     loadDbs(){
	        let this_ = this;        
	        let path = vueFlaskRouterConfig.infoSearchForDbSelection;
	        axios.get(path) // getpost.
	        .then((res) => {
	            // this_.loadingDbFlag = false;                
	            let data = res.data; // .            
	            this_.dboptions = data.dbname; // [{}, ...] 
	            // console.log("this_.dboptions");console.log(this_.dboptions);
              this_.$store.state.selection.dboptions = this_.dboptions; // [{value:x, label:x, directed:x}, ...]
              // bus.$emit("snedDboptions", this_.dboptions);
	            for(let i=0; i<this_.dboptions.length; i++){
	               let dbName = this_.dboptions[i].label;
	               let key_ = dbName.slice(0,-3); // .db,.
	               this_.dbLoadedFlag[key_] = false;  // {dbname1: false, ...} .
	            } 
	            
	            this_.$store.state.attrNode.discardfield = data.discardfield; // ,. [x, x, ...]
              this_.$store.state.attrNode.fullCompatibilityAtrr = data.fullCompatibilityAttr; // [x, x, ...] mainView,,,institution.
              // console.log("this_.$store.state.attrNode.discardfield");console.log(this_.$store.state.attrNode.discardfield);
              // console.log("this_.$store.state.attrNode.fullCompatibilityAtrr");console.log(this_.$store.state.attrNode.fullCompatibilityAtrr);
              this_.stopwordsObj = data.stopword; //,json.           
	                            
	          })
	        .catch((error) => {            
	          console.error(error);
	        });
     },
	},
	created(){ //.
      let this_ = this;
      console.log("loadDb.vue created");
      this_.$store.state.mainViewGraph.histClickFlag = this_.histClickFlag; // .
      this_.$store.state.infoSearchView.dbLoadedFlag = this_.dbLoadedFlag; // ,.
      this_.loadDbs(); // ,.
      
      bus.$on('sendDbName', function(data){
	      this_.$store.state.mainViewGraph.histClickFlag.splice(0, this_.$store.state.mainViewGraph.histClickFlag.length); // .
	      this_.$store.state.mainViewGraph.histClickFlag.push(true);  // .
	      if(this_.selectiondb == data){ //.
	         
	         bus.$emit("restoreFieldKeyWord", true);
	      }
	      this_.selectiondb = data;
	  });
    },
    mounted(){
      let this_ = this;
	  console.log("loadDb.vue mounted");
    },
    beforeDestroy(){
      bus.$off("sendDbName");
    }	
}

</script>

<style>
#load-db-view{
  margin: 0px 0px 0px 0px;
  /*padding:0px 0px 0px 0px;
  border-style: solid;
  border-color:#E0E0E0;
  border-top-width: 2px;
  border-right-width: 2px;
  border-bottom-width: 2px;
  border-left-width: 2px;*/
}
#load-db-view .el-input{
  /*border-style: solid;
  border-color:#E0E0E0;
  border-top-width: 2px;
  border-right-width: 2px;
  border-bottom-width: 2px;
  border-left-width: 2px;*/
}
#load-db-view .el-form-item{
  margin-bottom:5px;
}
#load-db-view .el-input__inner{
   height: 30px;
}
#load-db-view .el-form-item__content{
   line-height: 30px;
}
#load-db-view .el-input__suffix{
   height: 113%;
}
</style>


