<template>
<div id="overView-global-graph"> 
  <div id="overView-container">
        <!-- <div id="lasso-tool-box">
          <button id='lasso-tool-enable'>lasso</button>
          <button id='lasso-tool-cancel'>cancel</button>
          <button id='lasso-tool-lock'>lock</button>
        </div> -->
       <div id="graph-container"></div>
                  
    </div> 
    
  </div> 
</template>
<script>
   import $ from 'jquery'
   import axios from 'axios' // AJAX 
   import {vueFlaskRouterConfig} from '../vueFlaskRouterConfig'
   import bus from '../eventbus.js' // .  
 
   export default {
data(){
return {
        lasso: null, //.         
          sigmaObj: null, //sigma.
          graph: null,         
          previousSeletedNodes: [],
          currentSelectedNodes: [],
          nodeColor: "grey", //.
          nodeSize: 1, //.
          edgeColor: "grey",
          changeNodeColor: true,
          changeEdgeColor: true,
          // .
          selectedNodesInLasso: [], //. [{x:x, y:x, id:x}, ...]
          clickSubgraphFlag: true, // subgraph.
         
}
},
      props:[
        'globallayoutdata' //.
      ],
      watch:{

      },
methods:{
       setCanvasSize(selector, width, height){ // .
         
         $(selector).width(width); // 
         $(selector).height(height); // 
       },
       deleteEventInDiv(){
        // $("#view-graph > *").remove();
       },
       deleteGraphInSvg(svgId){
         $("#" + svgId + " > *").remove(); // svg,.
       },
       enableLasso(elId){ //.
          let this_ = this;
          $("#" + elId).click(function (){
            this_.lasso.activate();
            this_.sigmaObj.refresh({skipIndexation: true}); // {skipIndexation: true}
            console.log("click lasso");
          });
          
        },
        renderHalo(activeState) { // ,.
          let this_ = this;
          this_.sigmaObj.renderers[0].halo({
            nodes: activeState.nodes()
          });
        }
},
      created(){
        let this_ = this;
      },
      mounted(){
        let this_ = this;
        let selector = "#graph-container";
        let width =  this_.$store.state.globalGraphView.width; //.
        let height = this_.$store.state.globalGraphView.height; // 
        this_.setCanvasSize(selector, width, height);
        this_.deleteEventInDiv();        
        // $.getJSON("../../static/json/connected_max_subgraph_citation_network_visualization.json", function(data){
        this_.graph = this_.globallayoutdata;
        // console.log("globallayoutdata");
        // console.log(this_.graph);
        //.
        if(this_.changeNodeColor){
            this_.graph.nodes.forEach(function (item, index){
              item.color = this_.nodeColor;
              item.size = this_.nodeSize;
            });
        }
        if(this_.changeEdgeColor){
            this_.graph.edges.forEach(function (item, index){
              item.color = this_.edgeColor;
            });
        }

        sigma.renderers.def = sigma.renderers.canvas; //canvas.

        this_.sigmaObj = new sigma({
          graph: this_.graph,
          renderer: {
            container: document.getElementById('graph-container'),
            type: 'canvas' // webgl.
          },
          settings: { // src/sigma.settings.js.
            enableEdgeHovering: false,
            enableHovering: false,
            drawLabels: false,
            singleHover:true,
            borderSize: 2, //2
            outerBorderSize: 3,//3
            defaultNodeBorderColor: '#fff',
            defaultNodeOuterBorderColor: 'rgb(236, 81, 72)',
            // doubleClickZoomingRatio: 0,
            // zoomingRatio: 0,

            // .
            nodeHaloColor: '#fff',//'rgba(236, 81, 72, 0.2)',
            nodeHaloSize: 0, //,.
            nodeHaloStroke: false,
            nodeHaloStrokeColor: '#000',
            nodeHaloStrokeWidth: 0.5,
            nodeHaloClustering: false,
            nodeHaloClusteringMaxRadius: 1000,
            edgeHaloColor: '#fff',
            edgeHaloSize: 10,
            drawHalo: true,

            minNodeSize: 1,
            maxNodeSize: 2,
            minEdgeSize: 0.5,
            maxEdgeSize: 0.5,
          }
        });       

          
        let activeState = sigma.plugins.activeState(this_.sigmaObj); // sigma.
        let keyboard = sigma.plugins.keyboard(this_.sigmaObj, this_.sigmaObj.renderers[0]); //.

        

        
        let select = sigma.plugins.select(this_.sigmaObj, activeState); //.
        select.bindKeyboard(keyboard); //.
        
        // :.
        // let dragListener = sigma.plugins.dragNodes(this_.sigmaObj, this_.sigmaObj.renderers[0], activeState); //.

       
        this_.lasso = new sigma.plugins.lasso(this_.sigmaObj, this_.sigmaObj.renderers[0], { //. s.renderers[0] canvas.
          'strokeStyle': 'rgb(236, 81, 72)',
          'lineWidth': 2,
          'fillWhileDrawing': true,
          'fillStyle': 'rgba(236, 81, 72, 0.2)',
          'cursor': 'crosshair' //.
        });

        select.bindLasso(this_.lasso); //selectlasso.             

       
        

        this_.sigmaObj.renderers[0].bind('render', function(e) {
          this_.renderHalo(activeState); // ,.
        });
        
        keyboard.bind('32+83', function() { // .
          if (this_.lasso.isActive) { //.
            this_.lasso.deactivate(); //.
          } 
          else { //,.
            this_.lasso.activate();
          }
        });

    
        this_.lasso.bind('selectedNodes', function (event) { //.
          // console.log("selectedNodes");
          // console.log(event);
          let selectedNodes = event.data; //.
          this_.previousSeletedNodes = this_.currentSelectedNodes;
          this_.currentSelectedNodes = event;
          let idCurrentSelectedNodes = []; //id.
          for(let i=0; i<this_.currentSelectedNodes.data.length; i++){
             let obj = this_.currentSelectedNodes.data[i];
             idCurrentSelectedNodes.push(obj.id);
          }
          if(this_.previousSeletedNodes.data){ //.

              this_.previousSeletedNodes.data.forEach(function(item, index){
                if(idCurrentSelectedNodes.indexOf(item.id) == -1){ //.
                  item.color = this_.nodeColor; //.
                  item.active = false; //.
                }

              });
              this_.sigmaObj.refresh({skipIndexation: true}); // {skipIndexation: true}

          }


          setTimeout(function() {
              // this_.lasso.deactivate(); //.         
              this_.sigmaObj.refresh({skipIndexation: true}); //. { skipIndexation: true }
          }, 0);
          // console.log("selectedNodes");
          // console.log(selectedNodes);
          this_.selectedNodesInLasso = selectedNodes; //

          // console.log("previousSeletedNodes");
          // console.log(previousSeletedNodes);

          // console.log("currentSelectedNodes");
          // console.log(currentSelectedNodes);
        });
        // });
        // this_.enableLasso("lasso"); //.
        $("#lasso-icon-box").unbind(); //#lasso-icon-box,.
        $("#lasso-icon-box").click(function (e){ //.#lasso-icon-boxglobalGraphView.vue.           
            
            if(this_.clickSubgraphFlag){
              // console.log("sendSplitPanesFlag true");
              // bus.$emit("sendSplitPanesFlag", true); // .
              // this_.deleteGraphInSvg(this_.$store.state.globalFilteredSubgraphView.svgId); //,svg.
              this_.$store.state.globalFilteredSubgraphView.isOpenFilteredSubgraph = false; //.
              this_.clickSubgraphFlag = false;
              // this_.selectedNodesInLasso = []; // graph,,.
              let cssLasso = {
                "background":"#409EFF",
                "color":"#fff"
              };
              $("#lasso-icon-box").css(cssLasso); //.

              let cssSubGraph = {
                "background":"#ddd",
                "color":"#000"
              };
              $("#check-filter-subgraph").css(cssSubGraph); //.

              this_.lasso.activate(); // .
              this_.sigmaObj.refresh({skipIndexation: true}); // {skipIndexation: true}
              
            }

        });
        
        $("#check-filter-subgraph").unbind(); //#lasso-icon-box,.
        $("#check-filter-subgraph").click(function (e){ // #check-filter-subgraphglobalGraphView.vue.
            // bus.$emit("sendSplitPanesFlag", true); // .
            if(this_.clickSubgraphFlag === false){ //.
                this_.$store.state.globalFilteredSubgraphView.isOpenFilteredSubgraph = true; //.
                // bus.$emit("sendSplitPanesFlag", true); // .
                this_.clickSubgraphFlag = true;

                 let cssLasso = {
                   "background":"#ddd",
                   "color":"#000"
                };
                $("#lasso-icon-box").css(cssLasso); //

                let cssSubGraph = {
                  "background":"#409EFF",
                  "color":"#fff"
                };
                $("#check-filter-subgraph").css(cssSubGraph); //

                this_.lasso.deactivate(); //. 
                bus.$emit("sendSelectedNodesInLasso", this_.selectedNodesInLasso); // globalFilteredSubgraphView.vue.
               
                // todo:.
                // console.log("this_.selectedNodesInLasso");console.log(this_.selectedNodesInLasso);
            }
            
          });

      },
      destroyed(){
        
      }

    }
</script>
<style>
    
  /*kbd {
    background-color: #e7e7e7;
    background-image: -webkit-linear-gradient(#fefefe, #e7e7e7);
    background-image: linear-gradient(#fefefe, #e7e7e7);
    background-repeat: repeat-x;
    display: inline-block;
    padding: 3px 5px;
    font: 11px Consolas, "Liberation Mono", Menlo, Courier, monospace;
    line-height: 10px;
    color: #000;
    border: 1px solid #cfcfcf;
    border-radius: 2px;
  }

  .key.arrow {
     font-size: 15px;
     font-weight: bold
  }*/

  /*.over-view-nav {
    position: absolute;
    bottom: 0;
    right: 0;
    line-height: 30px;
    border: 1px solid #ccc;
    padding: 10px 20px;
    background: white;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 13px;
  }
  .left {
    display: inline-block;
    width: 100px
  }*/
  
  #graph-container{    
    position: relative; /*,canvas.*/
    /*width:200px;
    height:200px;*/
  }
  #overView-container{
    position: relative;
  }
  /*#lasso-tool-box{
   width:40px;
   height:40px;   
   top: 0;
   right: 0;
 }*/
  </style>


