<template>
  <div id="over-view-global-heat-map">  
     <div id="heat-map-box">
       <canvas id="graph-heat-map"></canvas>
     </div>
     <!-- <div id="lasso-tool-box">
       <button id='lasso-tool-enable'>lasso</button>
       <button id='lasso-tool-cancel'>cancel</button>
       <button id='lasso-tool-lock'>lock</button>
     </div> -->
  </div>
</template>

<script>
  import $ from 'jquery'
  import * as d3 from '../../static/js/d3.v4.min.js'
  // import '../../static/js/lasso.dist.js'
  import axios from 'axios' // AJAX 
  import {vueFlaskRouterConfig} from '../vueFlaskRouterConfig'
  import bus from '../eventbus.js' // .

  export default {    
    data: function(){
      return {
        xyPoints: [], //.[{x:1123, y:456}, ...]
        kdeWidth: 200, //.
        kdeHeight: 200,

        // : ,. h,.
        gridSize: 2, //
        h: 4, //h=
        qt: null,
        grid: null, // kdeWidth*kdeHeight  gridSize*gridSize ,grid=[{x:123, y:456}, ...],.
        outerScale: null, // .
        heatmapColor: null, //.

        //.
        lassoAreaColor: "#33FFCC", //.
        lassoLineColor: "#33FFCC", //.
        lassoStrokeDasharray: '3, 2', // + .
        fillOpacity: 0.2, //.
        strokeWidth: 2, // .

        // .
        selectedNodesInLasso: [], //. [{x:x, y:x, id:x}, ...]
        clickSubgraphFlag: true, // subgraph.
      }
    },
    props:[
     'globallayoutdata' //.
    ],
    methods:{
      deleteEventInDiv(){
        // $("#view-graph > *").remove();
      },
      deleteGraphInSvg(svgId){
         $("#" + svgId + " > *").remove(); // svg,.
       },
      polygonToPath(polygon) { //.
        return ("M" + (polygon.map(function (d) { return d.join(','); }).join('L')));
      },
      lasooDistance(pt1, pt2) { //.
        return Math.sqrt(Math.pow( (pt2[0] - pt1[0]), 2 ) + Math.pow( (pt2[1] - pt1[1]), 2 ));
      },      
      lassoTool() { // ,
        let this_ = this; // .
        let dispatch = d3.dispatch('start', 'end'); // 'start'+'end',click,dlclick.

        // distance last point has to be to first point before it auto closes when mouse is released
        let closeDistance = 75;
        let lockLasso = false; //,.

        function lasso(root) { // rootd3selection,,SVG.
          // append a <g> with a rect               
          let g = root.append('g').attr('class', 'lasso-group'); //g.
          let bbox = root.node().getBoundingClientRect(); // SVG, :{top:x,left:x,right:x,bottom:x,width:x, height:x}
          let area = g
            .append('rect')
            .attr('width', bbox.width)
            .attr('height', bbox.height)
            .attr('fill', 'tomato')
            .attr('opacity', 0); //grect:,SVG,SVG.

          let drag = d3 // d3:start,dragend, .
            .drag()
            .on('start', handleDragStart)
            .on('drag', handleDrag)
            .on('end', handleDragEnd);
          
          // console.log("maomaomao area.call");
          area.call(drag); //rect. drag  drag(area).

          let lassoPolygon; //
          let lassoPath; //:.
          let closePath; // : ,.

          function handleDragStart() { //.
            
            
            if(!lockLasso){
                // console.log("maomaomao handleDragStart");
                lassoPolygon = [d3.mouse(this)]; //,.
                // console.log("lassoPolygon handleDragStart");console.log(lassoPolygon);
                if (lassoPath) { // ,,.
                  lassoPath.remove(); // ,.
                }

                lassoPath = g // gpath.
                  .append('path')
                  .attr('fill', this_.lassoAreaColor) //. #0bb
                  .attr('fill-opacity', this_.fillOpacity) // this_.fillOpacity = 0.1
                  .attr('stroke', this_.lassoLineColor) //.
                  .attr('stroke-width', this_.strokeWidth) //
                  .attr('stroke-dasharray', this_.lassoStrokeDasharray); // . '3, 3':, .

                closePath = g
                  .append('line')
                  .attr('x2', lassoPolygon[0][0])  // . lassoPolygon=[[x, y], ...]
                  .attr('y2', lassoPolygon[0][1])
                  .attr('stroke', this_.lassoLineColor) // '#0bb'
                  .attr('stroke-width', this_.strokeWidth) //
                  .attr('stroke-dasharray', this_.lassoStrokeDasharray)
                  .attr('opacity', 0); // opacity.

                dispatch.call('start', lasso, lassoPolygon);

            }
            
          }

          function handleDrag() {
            if(!lockLasso){
                // console.log("maomaomao handleDrag");
                let point = d3.mouse(this);
                lassoPolygon.push(point); // lassoPolygon,lassoPolygon.
                lassoPath.attr('d', this_.polygonToPath(lassoPolygon)); //,,.

                // indicate if we are within closing distance
                if (
                  this_.lasooDistance(lassoPolygon[0], lassoPolygon[lassoPolygon.length - 1]) <
                  closeDistance
                ) { // ,.
                  closePath
                    .attr('x1', point[0])
                    .attr('y1', point[1])
                    .attr('opacity', 1); //line.
                } else {
                  closePath.attr('opacity', 0); //.
                }
            }
            
          }

          function handleDragEnd() {
            if(!lockLasso){
                // console.log("maomaomao handleDragEnd");
                // remove the close path
                closePath.remove(); // line,.
                closePath = null;

                // succesfully closed
                if (
                  this_.lasooDistance(lassoPolygon[0], lassoPolygon[lassoPolygon.length - 1]) <
                  closeDistance
                ) {
                  lassoPath.attr('d', this_.polygonToPath(lassoPolygon) + 'Z'); // SVGpathd,:<path d="M120, 210L120, 110L110, 120Z"></path>(120, 210),(120, 110),(110, 120),.
                  dispatch.call('end', lasso, lassoPolygon);

                  // otherwise cancel
                } else {
                  lassoPath.remove();
                  lassoPath = null;
                  lassoPolygon = null;
                }
            }
            
          }

          lasso.reset = function () {
            if (lassoPath) {
              lassoPath.remove();
              lassoPath = null;
            }

            lassoPolygon = null;
            if (closePath) {
              closePath.remove();
              closePath = null;
            }
          };
        }

        lasso.on = function (type, callback) {
          dispatch.on(type, callback);
          return lasso;
        };
        lasso.setLockLasso = function (flag){ // lassoLock,.
            lockLasso = flag;
        }
        // lasso.remove = function (){

        // }

        return lasso;
      },
      handleLassoEnd(lassoPolygon) { //.
        let this_ = this;
        console.log("handleLassoEnd");
        let selectedPoints = this_.xyPoints.filter(function (d) { // [{x:123, y:456}, ...]
             let x = d.x;
             let y = d.y;
             // lassoPolygon=[[x, y], [x, y], ...]
             return d3.polygonContains(lassoPolygon, [x, y]); //d3polygonContains[x, y],dselectedPoints.
        });
        // console.log(selectedPoints);
        this_.selectedNodesInLasso = selectedPoints; //[{}, {}, {}, ...]

      },
      // reset selected points when starting a new polygon
      handleLassoStart(lassoPolygon) {
         console.log("start");
      },
      computeDensities (param) {
        /*
      param={
          bandwidth: 4, //,bandwidthComputedMode=="common".
          densitiesComputedMode:"speedup", //,"speedup":barneshut,"common":.
          bandwidthComputedMode:"common" //,"common":,bandwidth. "adaption":,bandwidth.
      }
         :.
         
         hComputedMode:."adaption":, "common":.
        */
        let this_ = this;
        // Density at each (x,y) coordinate in the grid
        let densities = [];

        // Update bandwidth
        // Use same bandwidth for each dimension
        // x,: xyPoints:[{x:1, y:2}, {x:44, y:6}, {x:7, y:9}] => xPoints:[1, 7, 44]
        let xPoints = this_.xyPoints.map(function(d) { return d.x }).sort(function(a,b) { return a - b });
        
        //,.
        if(param.bandwidthComputedMode == "common"){ // 
          this_.h = param.bandwidth;
        }
        if(param.bandwidthComputedMode == "adaption"){ //
          let iqr = d3.quantile(xPoints, 0.75) - d3.quantile(xPoints, 0.25); //iqr =  - 
          this_.h = 1.06 * Math.min(d3.deviation(xPoints), iqr / 1.34) * Math.pow(this_.xyPoints.length, -0.2); // d3.deviation:.
        }
        if(param.densitiesComputedMode == "common"){ //.
            // Compute KDE for each (x,y) pair and update the color scale
            densities = this_.grid.map(function(point) { return this_.kdeBrute(point); }); //densities.length == grid.length
      //      outerScale.domain([0, d3.max(densities)]); //[0, d3.max(densities)]
      //      drawHeatmap(bruteContext, densities);
        }
        if(param.densitiesComputedMode == "speedup"){ //barneshut.
            let pointsList = []; //[[x, y], [x, y], ...]
            this_.xyPoints.forEach(function (p){
              let x = p.x;
              let y = p.y;
              pointsList.push([x, y]);
            });
            this_.qt = d3.quadtree(pointsList)
              .extent([[0, 0], [this_.kdeWidth, this_.kdeHeight]])
              .visitAfter(this_.accumulate);
            densities = this_.grid.map(function(point) { return this_.kdeBarneshut(point); });
      //      outerScale.domain([0, d3.max(densities)]); //[0, d3.max(densities)]
      //      drawHeatmap(barneshutContext, densities);
        }
        return densities;

      },
      renderHeatMap(densities, canvasContext){ //API.
        /*
        densities: [x, x, ...].
        canvasContext: .
        */
        let this_ = this;
        this_.outerScale.domain([0, d3.max(densities)]); //[0, d3.max(densities)]
        this_.drawHeatmap(canvasContext, densities);
      },
      drawHeatmap(context, densities) {
        let this_ = this;
        // Draw the grid
        //,
        this_.grid.forEach(function(point, idx) { //densitiesgrid.
          context.beginPath(); //
          context.fillStyle = this_.heatmapColor(this_.outerScale(densities[idx])); //.
          // Subtract to get the corner of the grid cell
          context.rect(point.x - this_.gridSize/2, point.y - this_.gridSize/2, this_.gridSize, this_.gridSize);
          context.fill();
          context.closePath();
        });
      },
      accumulate(quad) {
        // Sum of x/y coordinates
        let sx = 0;
        let sy = 0;

        let count = 0, q;

        // This quadrant has children.
        if (quad.length) {
          let i = -1;
          let c;
          while (++i < 4) {
            c = quad[i];
            if (c == null) continue;
            count += c.count;
            sx += c.sx;
            sy += c.sy;
          }
        }

        // This is a leaf node.
        else {
          q = quad;
          do {
            count += 1;
            sx += q.data[0];
            sy += q.data[1];
          } while (q = q.next);
        }

        quad.sx = sx;
        quad.sy = sy;
        quad.count = count;
      },
      kdeBrute(gridPoint) { //gridPoint , h()
        // xyPoints=[{x:1, y:2}, {x:3, y:4}, {x:5, y:6}]  gridPoint={x:4.5, y:6.5}  :,:distance(p, gridPoint) / h
        /*
          gridPoint,xyPoints,=1,.:
          1. gridPointxyPoints,h,gridPoint,,/(h*h),gridPoint.
        */
        let this_ = this;
        return d3.mean(this_.xyPoints, function(p) { return this_.kernelFunction(this_.distance(p, gridPoint) / this_.h); });// / (h*h); //.
      },

      kdeBarneshut(gridPoint) {
        let this_ = this;
        let estimate = 0;
        // let theta2 = 0.64; // 0.64
        let theta2 = 0.64;      

        this_.qt.visit(function (quadNode, x1, y1, x2, y2) {
          
          // Average x/y coordinates in this quadrant.
          let xx = quadNode.sx/quadNode.count;
          let yy = quadNode.sy/quadNode.count;
          let p = {x: xx, y: yy}; //
          let dx = p.x - gridPoint.x;
          let dy = p.y - gridPoint.y;
          let dw = x2 - x1;
          let dist = dx * dx + dy * dy;

          // Barnes-Hut: If the quadrant size is small relative to its
          // distance from the grid point, then aggregate the points in
          // that quadrant and treat them as a single large point (or many
          // individual points all in the same location).
          if (dw * dw / theta2 < dist && !(gridPoint.x >= x1 && gridPoint.x < x2 && gridPoint.y >= y1 && gridPoint.y < y2)) {
            estimate += quadNode.count * this_.kernelFunction(this_.distance(p, gridPoint) / this_.h);
            return true;
          }

          if (quadNode.point) {
            let distanceHere = Math.sqrt((quadNode.point[0] - gridPoint.x) * (quadNode.point[0] - gridPoint.x) + (quadNode.point[1] - gridPoint.y) * (quadNode.point[1] - gridPoint.y));
            estimate += this_.kernelFunction(distanceHere / this_.h);
          }

          // Stop recursing if there are no more nodes.
          return !quadNode.count;
        });

        return estimate / this_.xyPoints.length;
      },
      
      distance(v1, v2) { //v1v2.
        return Math.sqrt((v1.x - v2.x) * (v1.x - v2.x) + (v1.y - v2.y) * (v1.y - v2.y));
      },

      kernelFunction(x) { //: =0,=1
        // sqrt(2 * PI) is approximately 2.5066
        let mode = "Gaussian"; //.

        if(mode == "Gaussian"){
          return Math.exp(-x * x / 2) / 2.5066;
        }

        if(mode == "Quartic"){
           return 0.9375*Math.pow((1 - x*x), 2);
        }

        if(mode == "Epanechnikov"){
          return 0.75*(1 - x*x);
        }

        if(mode == "Triweight"){
          return 1.09375*Math.pow((1 - x*x), 3);
        }

      }

    },
    created(){
      let this_ = this;
      this_.kdeWidth = this_.$store.state.globalGraphView.width; // .
      this_.kdeHeight = this_.$store.state.globalGraphView.height;
      //.
      this_.grid = d3.merge(d3.range(0, this_.kdeHeight/this_.gridSize).map(function(i) {
          return d3.range(0, this_.kdeWidth/this_.gridSize).map(function(j) { return {x: j*this_.gridSize + this_.gridSize/2, y: i*this_.gridSize + this_.gridSize/2} });
      }));
      this_.outerScale = d3.scalePow() //. y=f(x)
                         .exponent(0.4) //=0.4
                         .domain([0,1]) //
                         .range([0,1]); //
      this_.heatmapColor = d3.scaleLinear() // y=f(x)
                        .clamp(true)
                        .domain([0, 0.1111111111111111, 0.2222222222222222, 0.3333333333333333, 0.4444444444444444, 0.5555555555555555, 0.6666666666666666, 0.7777777777777777, 0.8888888888888888, 1])
                        .range(['#ffffff','#fff7f3','#fde0dd','#fcc5c0','#fa9fb5',
                          '#f768a1','#dd3497','#ae017e','#7a0177','#49006a']);
    },
    mounted(){
      let this_ = this;
      this_.deleteEventInDiv(); //DIV.
      let visRoot = d3.selectAll("#heat-map-box") // div: canvas + svg, canvas:, svg:.
                      .style('position', 'relative');

      let canvasContext = d3.selectAll("#graph-heat-map")
                            .attr("width", this_.kdeWidth)
                            .attr("height", this_.kdeHeight);
      let graphCanvasContext = canvasContext.node().getContext("2d"); //2D.

      // d3.json("../../static/json/connected_max_subgraph_citation_network_visualization.json", function(error, graph_) {
         let graph = JSON.parse(JSON.stringify(this_.globallayoutdata)); //
         let nodes = graph["nodes"]; //[{id:"1234", x:1.2, y:3.3}, ...]
         //:nodes.
         let xMaxVal = -1;
         let xMinVal = 100000;
         let yMaxVal = -1;
         let yMinVal = 100000;

         nodes.forEach(function (node, idx){
            let x = node.x;
            let y = node.y;

            //x.
            if(x >= xMaxVal){
              xMaxVal = x;
            }

            //x
            if(x <= xMinVal){
              xMinVal = x;
            }

            //y
            if(y >= yMaxVal){
              yMaxVal = y;
            }

            //y
            if(y <= yMinVal){
              yMinVal = y;
            }
         });

         let fx = this_.kdeWidth / (xMaxVal - xMinVal); //x.
         let fy = this_.kdeHeight / (yMaxVal - yMinVal); //y.

         nodes.forEach(function (node, idx){
            let x = node.x * fx - fx*xMinVal;
            let y = node.y * fy - fy*yMinVal;
            let nodeId = node.id; //id.
            this_.xyPoints.push({x: x, y: y, id:nodeId}); //xyPoints,{x:x, y:y, id:nodeId}
         });
        
         let param = {};
         if(this_.$store.state.infoSearchView.numNodes < 3000){ // 3000,.
            param={
               bandwidth: 4, //,bandwidthComputedMode=="common".
               densitiesComputedMode:"common", //,"speedup":barneshut,"common":,vue,,.
               bandwidthComputedMode:"common" //,"common":,bandwidth. "adaption":,bandwidth.
            };
         }
         else{
            param={
              bandwidth: 4, //,bandwidthComputedMode=="common".
              densitiesComputedMode:"speedup", //,"speedup":barneshut,"common":,vue,,.
              bandwidthComputedMode:"common" //,"common":,bandwidth. "adaption":,bandwidth.
            };
         }
        
         //  let param={
         //    bandwidth: 4, //,bandwidthComputedMode=="common".
         //    densitiesComputedMode:"common", //,"speedup":barneshut,"common":,vue,,.
         //    bandwidthComputedMode:"common" //,"common":,bandwidth. "adaption":,bandwidth.
         // };
         // TODO: ,.
         let densities = this_.computeDensities(param);
         let canvasContext_ = graphCanvasContext;
         this_.renderHeatMap(densities, canvasContext_);
         
         // add in an interaction layer as an SVG
         let interactionSvg = visRoot // SVG.
            .append('svg')
            .attr('width', this_.kdeWidth)
            .attr('height', this_.kdeHeight)
            .style('position', 'absolute') // SVGcanvas.
            .style('top', 0)
            .style('left', 0);

          //attach lasso to interaction SVG
          // let lassoInstance = this_.lasso()
          //     .on('start', this_.handleLassoStart)
          //     .on('end', this_.handleLassoEnd);
          // interactionSvg.call(lassoInstance);
          
          // .
          let lassoInstance = this_.lassoTool()
                .on('start', this_.handleLassoStart)
                .on('end', this_.handleLassoEnd); 
          
          $("#lasso-icon-box").unbind(); //#lasso-icon-box,.
          $("#lasso-icon-box").click(function (e){ //.#lasso-icon-boxglobalGraphView.vue.
            if(this_.clickSubgraphFlag){
                // console.log("heatMap");
                this_.deleteGraphInSvg(this_.$store.state.globalFilteredSubgraphView.svgId); //,svg.
                this_.clickSubgraphFlag = false;
                this_.$store.state.globalFilteredSubgraphView.isOpenFilteredSubgraph = false; //.
                this_.selectedNodesInLasso = []; // .
                this_.$store.state.globalFilteredSubgraphView.globalFilteredGraph = null; //store.js.
                let cssLasso = {
                  "background":"#409EFF",
                  "color":"#fff"
                };
                $("#lasso-icon-box").css(cssLasso); //.

                let cssSubGraph = {
                  "background":"#ddd",
                  "color":"#000"
                };
                $("#check-filter-subgraph").css(cssSubGraph); //.

                lassoInstance.setLockLasso(false);
                // attach lasso to interaction SVG 
                interactionSvg.selectAll("g").remove(); // svgg.                 
                interactionSvg.call(lassoInstance); //SVG.lassoInstance(interactionSvg), .
                bus.$emit("clearGraphEVFlag", true); //|V| + |E|
               
            }
            

          });
      
          $("#check-filter-subgraph").unbind(); //#lasso-icon-box,.
          $("#check-filter-subgraph").click(function (e){ // #check-filter-subgraphglobalGraphView.vue.
            if(this_.clickSubgraphFlag === false){
                this_.clickSubgraphFlag = true;
                this_.$store.state.globalFilteredSubgraphView.isOpenFilteredSubgraph = true; //.
                let cssLasso = {
                   "background":"#ddd",
                   "color":"#000"
                };
                $("#lasso-icon-box").css(cssLasso); //.

                let cssSubGraph = {
                  "background":"#409EFF",
                  "color":"#fff"
                };
                $("#check-filter-subgraph").css(cssSubGraph); //.

                lassoInstance.setLockLasso(true); //.
                bus.$emit("sendSelectedNodesInLasso", this_.selectedNodesInLasso); // globalFilteredSubgraphView.vue.
                
                // todo:.
                console.log("this_.selectedNodesInLasso");console.log(this_.selectedNodesInLasso);
                // bus.$emit("sendViewResize", 30); // resize.


            }
            
          });
    // });
    },
    beforeDestroy(){
      
    } 
  }
</script>

<style>
#over-view-global-heat-map{
  position: relative;
}
 /*#lasso-tool-box{
   width:40px;
   height:40px;
   position: absolute;
   top: 0;
   right: 0;
 }*/
 #lasso-tool{
   /*width:40px;
   height:40px;*/

 }
</style>


