<template> 
  <div id="foucus-set">
    <el-collapse v-model="activeNames">
      <el-collapse-item title="Focus Set" name="Focus Set"> 
          <!-- <span class="focus-num-span" v-if="numTag == 1"><strong>{{numTag}} Focus</strong></span>
          <span class="focus-num-span" v-if="numTag > 1"><strong>{{numTag}} Focuses</strong></span> -->
          <!-- <span class="focus-num-span" v-if="numTag == 1">{{numTag}} Focus</span>
          <span class="focus-num-span" v-if="numTag > 1">{{numTag}} Focuses</span> -->
          <div v-if="numTag > 0" id="focusesbox">    
            <el-tag
            v-for="(tag,index) in dynamicTagsFocus"
            :key="tag.id"
            :class= "tag.id"       
            closable
            :disable-transitions="false"
            @close="handleClose(tag)">
                <span :id="index" class="tag-focus" v-if="tag.tag.length > 30">{{tag.tag.slice(0,31)+"..."}}</span>
                <span :id="index" class="tag-focus" v-else>{{tag.tag}}</span>         
            </el-tag>
          </div>
          <div id="doibuttonbox" v-if="dynamicTagsFocus.length > 0">
            <el-button size="small" type="primary" @click="getSubgraph">Start</el-button>
          </div>
          <div class="tagInfoContent"></div>  <!--,--> 
      </el-collapse-item>
    </el-collapse> 
   
  </div>
</template>

<script>
import $ from 'jquery'
import axios from 'axios' // AJAX
// import qs from 'qs' 
import {vueFlaskRouterConfig} from '../vueFlaskRouterConfig'
import bus from '../eventbus.js' // .
  export default {
    data() {
      return {
        /*         
        dynamicTagsFocus=[{
          attributesValue:{institution: "xxx", interests: "xxx"},
          attriselect:["institution", "interests"],
          dbname: "Coauthor_Visualization.db",
          field: "institution",
          id: "1620830",
          keyword: "University of Utah",
          tag: "Fangxiang Jiao"
        }, ...]
        bus.$on('sendSlectedNode').
        :dynamicTagsFocuscreated(),dynamicTagsFocusthis_.$store.state.focusSetView.dynamicTagsFocus,this_.$store.state.focusSetView.dynamicTagsFocus,dynamicTagsFocus.,.
        */
        dynamicTagsFocus: [], // [{}, ...],,let mao=dynamicTagsFocus,mao.js.
        nodenum: null, // .
        focusSet:[], // id,id,idString,[xxx, xxx, ...]
        tagName: "",
        xOffset: 10,  // .
        yOffset: 45       
        
      };
    },
    computed:{
      numTag: function(){ // .
         let num = this.dynamicTagsFocus.length; // .
         return num;
      },
      activeNames:{ // activeNamesv-model,getter + setter,.
          // getter
          get: function(){
            if(this.dynamicTagsFocus.length > 0){ // 
                let temp = [];
                temp.push("Focus Set");
                return temp;
            }
            else{
              let temp = [];
              return temp;
            }           
         },
         // setter
         set: function(){
          
         }
       }
    },
    watch:{  // data,v-model,data.
      dynamicTagsFocus: function(curVal, oldVal){                
         console.log("curVal");
         console.log(curVal);
         bus.$emit("sendFocusNodeNumber", curVal.length); // mainView.vue.
      }
    },
    updated(){ // updated().
      let this_ = this; 
      if(this_.dynamicTagsFocus.length > 0){
        $(".tag-focus").unbind();  // :,.
        $(".tag-focus").mouseover(function(e){ // .
              // console.log("tag-focus e");console.log(e);
              let tagId = parseInt(e.currentTarget.id); // idint.              
              let nodeId = e.currentTarget.parentElement.classList[1]; //id.
              $("#focusesbox ." + nodeId).css("border", "1px solid #2894FF"); //.             
              
              let attributesExplorNodeHighlight = this_.$store.state.mainViewGraph.attributesExplorNodeHighlight;
              if(attributesExplorNodeHighlight.length > 0){ // .
                 attributesExplorNodeHighlight.forEach(function(item, index){
                        
                   let css = {
                      "stroke-dasharray":0,
                      "stroke": this_.$store.state.mainViewGraph.highlightColorSheet.clickMatchNodeAttri,
                      "stroke-width": 3
                    };
                    $("#mainsvg #" + item).css(css); // ,.
                });
              }
              let css = {
                  "stroke-dasharray":0,
                  "stroke": "black", // #1890ff
                  "stroke-width": 3
              };
              $("#mainsvg #" + this_.dynamicTagsFocus[tagId].id).css(css); //mainView.              
              $("#" + this_.$store.state.resultsSearchView.svgId + " ." + this_.dynamicTagsFocus[tagId].id).css(css); //results vis.
              $("#" + this_.$store.state.globalFilteredSubgraphView.svgId + " ." + this_.dynamicTagsFocus[tagId].id).css(css); //filtered subgraph View.             

               //
              let mousePos = this_.mousePosition(e);
              let  xOffset = this_.xOffset;
              let  yOffset = this_.yOffset;
              $(".tagInfoContent").css("display","block").css("position","absolute")
              .css("top",(mousePos.y - yOffset) + "px")
              .css("left",(mousePos.x + xOffset) + "px");

              // TODO: .
              let stringHtml="<div class='here-focus-info-box'><p><strong>id</strong>: %s<br><strong>name</strong>: %s<br><strong>graph data</strong>: %s<br><strong>selected atrribute</strong>: %s<br><strong>keywords</strong>: %s<br><strong>attributes of interest</strong>: %s</p></div>";
              let attriselect = null;  
              if(this_.dynamicTagsFocus[tagId].attriselect.length > 0){
                 attriselect = this_.dynamicTagsFocus[tagId].attriselect;
              }
              else{
                attriselect = "NULL";
              }
                                             
              let html = this_.sprintf(stringHtml, this_.dynamicTagsFocus[tagId].id, this_.dynamicTagsFocus[tagId].tag, this_.dynamicTagsFocus[tagId].dbname, this_.dynamicTagsFocus[tagId].field,  this_.dynamicTagsFocus[tagId].keyword, attriselect);
              $(".tagInfoContent").append(html);
              
                  
           });
         //,.
         $(".tag-focus").mouseout(function(e){
             // console.log("tag-focus e mouseout");console.log(e);
             let nodeId = e.currentTarget.parentElement.classList[1]; //id.
             //result vis
             let restoreCss = {
                "stroke-dasharray":0,
                "stroke": "#fff",
                "stroke-width": 0
              };
             $("#" + this_.$store.state.resultsSearchView.svgId + " ." + nodeId).css(restoreCss); //.
             $("#" + this_.$store.state.globalFilteredSubgraphView.svgId + " ." + nodeId).css(restoreCss); //filtered subgraph View.

             $("#focusesbox ." + nodeId).css("border", "1px solid rgba(64,158,255,.2)"); //.
             $(".tagInfoContent").empty();
             $(".tagInfoContent").css("display","none");
             let css_ = {
                          "stroke-dasharray":0,
                          "stroke": "#fff",
                          "stroke-width": 0
             };
            $("#mainsvg .nodecircle").css(css_);  // fixme: ,.
            let attributesExplorNodeHighlight = this_.$store.state.mainViewGraph.attributesExplorNodeHighlight;
              if(attributesExplorNodeHighlight.length > 0){ // .
                 attributesExplorNodeHighlight.forEach(function(item, index){
                        
                   let css = {
                      "stroke-dasharray":0,
                      "stroke": this_.$store.state.mainViewGraph.highlightColorSheet.clickMatchNodeAttri,
                      "stroke-width": 3
                    };
                    $("#mainsvg #" + item).css(css);
                });
              }            
         });
      }            
         
    },
    created(){
      let this_ = this;       
      this_.$store.state.focusSetView.dynamicTagsFocus = this_.dynamicTagsFocus;  // dynamicTagsFocusstore.js. :.
      bus.$on('sendSlectedNode', function(data){ //,.        
          let id = data.id; // id,.
          let isexistence = false; // . .
          for(let i=0; i<this_.dynamicTagsFocus.length; i++){
             if(this_.dynamicTagsFocus[i].id == id){
                isexistence = true; // true,.
                break;
             }
          }
          if(isexistence === false){ // ,. 
            this_.dynamicTagsFocus.push(data); // . .                    
          }
          /*
            data={
              id: "1620830", // id.
              tag: "Fangxiang Jiao" // .
              attriselect:["institution", "interests"], //()
              attributesValue:{institution: "xxx", interests: "xxx"}, //.               
              dbname: "Coauthor_Visualization.db",
              field: "institution", // .                
              keyword: "University of Utah", // .                
            }
          */
          
      });
      bus.$on("nodeNum", function(data){ // ,nodenum
          this_.nodenum = data; // data int .
          //console.log("this_.nodenumjinjing");console.log(data);

      });
      bus.$on("focusSetClear", function(data){
        let labelsClear = data; // .
        if(labelsClear){
          this_.dynamicTagsFocus.splice(0, this_.dynamicTagsFocus.length); // ().
        }
      });
      bus.$on("sendDynamicTagsFocus", function(data){
        
        this_.dynamicTagsFocus.splice(0,this_.dynamicTagsFocus.length);  // . 
        for(let i=0; i<data.length; i++){
            this_.dynamicTagsFocus.push(data[i]);           
        }       
        
      });

      // bus.$on("sendSelectedFocus", function(data){
      //     this_.dynamicTagsFocus.push(data);  // .          
      // });

      bus.$on("sendDeleteFocusInResultVis", function(data){
          //data = [nodeId, param, this_.isOpenResultVis]
         let nodeId = data[0];
         let param = data[1]; //, result_table / result_vis
         let isOpenResultVis = data[2]; //result vis.
         for(let i=0; i<this_.dynamicTagsFocus.length; i++){
           let tag = this_.dynamicTagsFocus[i]; //{id:x, tag:x, ...}
           if(tag.id == nodeId){ //.
             this_.dynamicTagsFocus.splice(i, 1);
             break; //,.
           }
         }
         if(isOpenResultVis){ //result vis.
           $("#" + param.svgId + " ." + nodeId).css("fill", param.nodeColor); //,,.
         }         
         $("#search-result-table ." + nodeId + " td:first-child").css("background-color", ""); //,..  #fff 
         // $("#search-result-table ." + nodeId + " td:first-child").css("background-color", "auto"); //,..       
        
      });
      bus.$on("sendDeleteFocusInRankingList", function(data){
        //data = [nodeId, param, this_.isOpenResultVis]
         let nodeId = data;
         // let param = data[1]; //, result_table / result_vis
         // let isOpenResultVis = data[2]; //result vis.
         for(let i=0; i<this_.dynamicTagsFocus.length; i++){
           let tag = this_.dynamicTagsFocus[i]; //{id:x, tag:x, ...}
           if(tag.id == nodeId){ //.
             this_.dynamicTagsFocus.splice(i, 1);
             break; //,.
           }
         }                  
         $("#ranking-by-node-measures ." + nodeId + " td:first-child").css("background-color", ""); //,.. 
          
      });
      bus.$on("sendDeleteFocusInFilteredSubgraph", function (data){
         // data = [nodeId, param, this_.isOpenFilteredSubgraph]
         let nodeId = data[0];
         let param = data[1]; //, result_table / result_vis
         let isOpenFilteredSubgraph = data[2]; //result vis.
         for(let i=0; i<this_.dynamicTagsFocus.length; i++){
           let tag = this_.dynamicTagsFocus[i]; //{id:x, tag:x, ...}
           if(tag.id == nodeId){ //.
             this_.dynamicTagsFocus.splice(i, 1);
             break; //,.
           }
         }
         
         if(isOpenFilteredSubgraph){ //filtered graph,.
           console.log("cancel highlight");
           $("#" + param.svgId + " ." + nodeId).css("fill", param.nodeColor); //,,.
           console.log(param.svgId);
           console.log(param.nodeColor);
           
         }

      });  
    },
    methods:{
      highLightNodeInSvg(svgId){ //SVG.
        
      },
      restoreNodeStyleInSvg(svgId){ //SVG.highLightNodeInSvg.

      },
      doiGraphInfo(data){
            let doiGraphInfo = {};
            let graph = data.graph;
            let directed = data.directed;
            doiGraphInfo.numnodes = graph.nodes.length;
            doiGraphInfo.numedges = graph.links.length;
            doiGraphInfo.directed = directed;
            bus.$emit("sendDoiGraphInfo", doiGraphInfo);  
      },
      mousePosition(ev){ 
          ev = ev || window.event; 
          if(ev.pageX || ev.pageY){ 
              return {x:ev.pageX, y:ev.pageY}; 
          } 
          return { 
              x:ev.clientX + document.body.scrollLeft - document.body.clientLeft, 
              y:ev.clientY + document.body.scrollTop - document.body.clientTop 
          }; 
      },
      sprintf(){  // .
        let args = arguments,
        string = args[0],
        i = 1;
        return string.replace(/%((%)|s|d)/g, function (m) {
            // m is the matched format, e.g. %s, %d
            let val = null;
            if (m[2]) {
                val = m[2];
            } else {
                val = args[i];
                // A switch statement so that the formatter can be extended. Default is %s
                switch (m) {
                    case '%d':
                        val = parseFloat(val);
                        if (isNaN(val)) {
                            val = 0;
                        }
                        break;
                }
                i++;
            }
            return val;
        });
      },
      getSubgraph(){
        let this_ = this;          
        console.log("graph!"); 

        // TODO:,. e.g. [1234, 4532, 5556]
        //this_.focusSet = []; // ,dynamicTagsFocus,.  bus.$emit("sendGraphData").d3.
        this_.focusSet.splice(0,this_.focusSet.length); // ,dynamicTagsFocus,.  bus.$emit("sendGraphData").d3.
        let idNameObj = {}; // , {1id: 1, 2id: 2}
        for(let i=0; i<this_.dynamicTagsFocus.length; i++){
          let id = this_.dynamicTagsFocus[i].id;
          // console.log("typeof(id)");console.log(typeof(id)); // string
          idNameObj[id] = this_.dynamicTagsFocus[i].tag;  // {id: tag, ...}
          this_.focusSet.push(String(id)); // .id. String,.
         }
        this_.$store.state.focusSetView.idNameObj = idNameObj;  // store.js.
        // 
        if(this_.dynamicTagsFocus.length > 0){ // ,Extract,.
          bus.$emit("sendDeleteSearchTableFlag", true);
        }
        let setInterests = {"setNodes": this_.dynamicTagsFocus, "setKeyword":{}}; // TODO: ,  + ,. :"setKeyword"{field1:[],...},.
        this_.$store.state.focusSetView.setInterests = setInterests;  // store.js. this_.$store.state.focusSetView.idNameObj = idNameObj;  // store.js.
         
        let diffFactor = this_.$store.state.widgetView.diffFactor;
        let UI_factor = this_.$store.state.widgetView.UIFactor;
        let API_factor = this_.$store.state.widgetView.APIFactor;
        let isEdgeAttri = this_.$store.state.widgetView.isEdgeAttri;
        let probRestart = this_.$store.state.widgetView.probRestart;
        let weightAttrNode = this_.$store.state.widgetView.weightAttrNode;
        let param = {"setInterests": setInterests, 
                     "scaleOfSingleFocus": this_.nodenum,
                     "diffFactor": diffFactor,
                     "UI_factor": UI_factor,
                     "API_factor": API_factor,
                     "isEdgeAttri": isEdgeAttri,
                     "probRestart": probRestart,
                     "weightAttrNode": weightAttrNode,
                     "isClickHist": false,
                     "interestSubgraphNodeList": []}; // "isClickHist",.
        
        bus.$emit("sendMainViewLoadingFlag", true); // .    
        
        axios.post(vueFlaskRouterConfig.mainViewGraph, { // ,DOI.
          param: JSON.stringify(param)
        })
        .then((res) => { 
              
                // TODO: +,DOI,.
                console.log("graph!");
                let data = res.data;  // ,id.
                // TODO: ,,.
                // TODO: ,,mainview.vue,,,,,.
                let graph = data.graph; 
                let directed = data.directed;
                let useSearchFlag = true; 
                // TODO: .
                let tempExplandNewNodeList = [];
                this_.$store.state.mainViewGraph.isExplandNodeFlag = false; // ,,.
                bus.$emit("sendGraphData", [graph, this_.focusSet, useSearchFlag, directed, tempExplandNewNodeList]); // mainView.vue, sendGraphData,graph+focus,:graph+focu,. 
                bus.$emit("sendClearExplandNewNodeListFlag", true); // mianView.vue,
                this_.doiGraphInfo(data); // DOI.      


          })
          .catch((error) => {            
            console.error(error);
          });

      },
      handleClose(tag) { //fixme:.
        let this_ = this;
        let nodeId = tag.id;
        // .
        this_.dynamicTagsFocus.splice(this_.dynamicTagsFocus.indexOf(tag), 1); // array.splice(index,howmany,item1,.....,itemX),splice() ,.
        this_.$store.state.resultsSearchView.focusStateList.splice(this_.$store.state.resultsSearchView.focusStateList.indexOf(nodeId), 1); //nodeId.
        
        // ,results vis + ,.
        $("#search-result-table ." + nodeId + " td:first-child").css("background-color", "");
        // $("#search-result-table ." + nodeId + " td:first-child").css("background-color", "auto"); //,..  #fff  
        $("#" + this_.$store.state.resultsSearchView.svgId + " ." + nodeId).css("fill", this_.$store.state.resultsSearchView.layoutSettings.nodeColor); //,,.   

        // ,filtered subgraph.
        $("#" + this_.$store.state.globalFilteredSubgraphView.svgId + " ." + nodeId).css("fill", this_.$store.state.globalFilteredSubgraphView.layoutSettings.nodeColor);
        
        this_.$store.state.nodeRankTable.focusStateList.splice(this_.$store.state.nodeRankTable.focusStateList.indexOf(nodeId), 1); //nodeId.
        $("#ranking-by-node-measures ." + nodeId + " td:first-child").css("background-color", "");

      },     
    },
    mounted(){
      let this_ = this;
      
    },    
    beforeDestroy () {
       bus.$off("nodeNum"); // DOI.
       bus.$off('sendSlectedNode');  // bus.on(),bus.$off(),.    
       bus.$off("focusSetClear");   // .    
       bus.$off("sendDynamicTagsFocus");
       // bus.$off("sendSelectedFocus");
       bus.$off("sendDeleteFocusInResultVis"); //results vis.
       bus.$off("sendDeleteFocusInFilteredSubgraph"); //filtered subgraph.
       bus.$off("sendDeleteFocusInRankingList"); // Ranking List.
    },       
  }
</script>
