<template>
  <div id="node_num_widget">
    <el-collapse v-model="activeNames" @change="handleChange">
       
      <el-collapse-item title="Control Panel" name="Control Panel">        
        <div id="settings-first-box">
          <div class="block-nodes-control settings-block">
            <span>ctrl_size_subgraph:</span>
            <el-slider v-model="scaleOfSingleFocus">    
            </el-slider>
            <!-- <span>max_num_neighbors</span>
            <el-slider v-model="explandneighbors">    
            </el-slider>  -->          
          </div>           
          <div class="block-doi settings-block">            
            <!-- <span>weight_API</span>
            <el-slider v-model="APIFactor" :max=1 :step=0.01>    
            </el-slider> -->
            <span>weight_UI:</span>
            <el-slider v-model="UIFactor" :max=1 :step=0.01>    
            </el-slider>
          </div>
          <!---->
          <div class="block-diffusion settings-block">
              <span>diffusion factor:</span>
              <el-slider v-model="diffFactor" :max=1 :step=0.01>    
              </el-slider>            
          </div>
        </div>
        <!-- <div id="settings-second-box">
          <div class="block-random-walk settings-block">
              <span>restart_prob</span>
              <el-slider v-model="probRestart" :max=1 :step=0.01>    
              </el-slider>
              <span>weight_attr_node</span>
              <el-slider v-model="weightAttrNode" :max=1 :step=0.01>    
              </el-slider>
          </div>
          <div class="block-diffusion settings-block">
              <span>diffusion factor</span>
              <el-slider v-model="diffFactor" :max=1 :step=0.01>    
              </el-slider>
            <span>Attribution_Edge_Diffusion</span>
            <el-switch
              v-model="isEdgeAttri"
              active-color="#5DADE2"
              inactive-color="#EAECEE">
            </el-switch>
          </div>         
        </div> -->
        
        
        <div class="reset-fault-settings">
          <el-button size="mini" type="primary" @click="onReset">Reset</el-button>
        </div>
        
      </el-collapse-item>
      <!-- <el-collapse-item title="Controls" name="Controls">
      </el-collapse-item> -->
    </el-collapse>

  </div>
</template>
<script>
  import axios from 'axios' // AJAX
  import * as d3 from 'd3'
  import {vueFlaskRouterConfig} from '../vueFlaskRouterConfig'
  import bus from '../eventbus.js' // .
  
  export default {
    data() {
      return {
        activeNames: [], // ,name.
        scaleOfSingleFocus:0, // DOI.
        explandneighbors: 0, // ,.
        diffFactor: 0, // FIXME: DOI.: DOI=API_factor*API + UI_factor*UI, DOI.,.
        UIFactor: 0,  // UI_Factor, .
        APIFactor: 0, // : UIUIFactor + APIFactor = 1.
        isEdgeAttri: true, // .DOI*.

        probRestart: 0,  // RWR.
        weightAttrNode: 0, // -.
      }
    },
    methods: {
       onReset(){          
          this.scaleOfSingleFocus = 30;  // scale Of Single Focus,.  
          this.explandneighbors = 5; // .  
          this.diffFactor = 0.85;  // .  
          this.UIFactor = 0.9;  // UI_Factor,,API_factor = 1 - UI_factor.
          this.APIFactor = 0.1;  // API_Factor
          this.isEdgeAttri = true; // DOI
          this.probRestart = 0.7;  // RWR.  
          this.weightAttrNode = 1; // -. 

          // scaleOfSingleFocus: 20,  // scale Of Single Focus,. this.$store.state.widgetView.scaleOfSingleFocus
          // explandneighbors: 10, // . this.$store.state.widgetView.explandneighbors
          // diffFactor: 1,  // . this.$store.state.widgetView.diffFactor
          // UIFactor: 0.9,  // UI_Factor,,API_factor = 1 - UI_factor.
          // APIFactor: 0.1,  // API_Factor
          // isEdgeAttri: false, // DOI
          // probRestart: 0.3,  // RWR. this.$store.state.widgetView.probRestart
          // weightAttrNode: 1, // -. this.$store.state.widgetView.weightAttrNode         
       },     
       handleChange(val) {  // (name).
        console.log(val);  // 1 + 2,3,[1,2,3]
      },
    },
    mounted(){
    	let this_ = this; 
      console.log("scaleOfSingleFocusNodeNum.vue mounted");      
      bus.$emit("explandNodeNum", this_.explandneighbors);
      bus.$on("Settings", function(settings){ // loadDb.vue, ,reset,.
        this_.scaleOfSingleFocus = settings.scaleOfSingleFocus;
        this_.explandneighbors = settings.explandneighbors;
        this_.diffFactor = settings.diffFactor; // .
        this_.UIFactor = settings.UIFactor; // UI.
        this_.APIFactor = settings.APIFactor; // API.
        this_.isEdgeAttri = settings.isEdgeAttri; // DOI,.
        this_.probRestart = settings.probRestart;  // RWR.
        this_.weightAttrNode = settings.weightAttrNode; // -.
        /*
        probRestart: 0.7,  // RWR. this.$store.state.widgetView.probRestart
      weightAttrNode: 1,*/
      });
    },
    created(){
       console.log("this.scaleOfSingleFocus created");
       let this_ = this;       
       bus.$emit("explandNodeNum", this_.explandneighbors);
       bus.$emit("nodeNum", this_.scaleOfSingleFocus);
      
      // fixme: store.jsdata.
      this_.scaleOfSingleFocus = this_.$store.state.widgetView.scaleOfSingleFocus;
      this_.explandneighbors = this_.$store.state.widgetView.explandneighbors;
      this_.diffFactor = this_.$store.state.widgetView.diffFactor; // .
      this_.UIFactor = this_.$store.state.widgetView.UIFactor; // UI.
      this_.APIFactor = this_.$store.state.widgetView.APIFactor; // API.
      this_.isEdgeAttri = this_.$store.state.widgetView.isEdgeAttri; // DOI,.
      this_.probRestart = this_.$store.state.widgetView.probRestart;  // RWR.
      this_.weightAttrNode = this_.$store.state.widgetView.weightAttrNode; // -.
      bus.$on("dbLoadedState", function(data){
         // todo: ,.

      });
    },
    computed:{
      // test(){
      //   alert("this.explandneighbors");
      //   alert(this.explandneighbors);
      // }
    },
    beforeDestroy(){       
       bus.$off('Settings');  // bus.on(),bus.$off(),. 
       bus.$off("dbLoadedState");  // .       
    },
    watch:{ // ,data,.
    	scaleOfSingleFocus: function(curVal, oldVal){
         //console.log(this.scaleOfSingleFocus);
         bus.$emit("nodeNum", curVal); // nodeNum,. 
         this.$store.state.widgetView.scaleOfSingleFocus = curVal;
    	},
      explandneighbors: function(curVal, oldVal){
         bus.$emit("explandNodeNum", curVal); // explandNodeNum,.
         this.$store.state.widgetView.explandneighbors = curVal;
      },
      diffFactor: function(curVal, oldVal){
        this.$store.state.widgetView.diffFactor = curVal; // store.
      },
      UIFactor: function(curVal, oldVal){
        this.$store.state.widgetView.UIFactor = curVal; // UI.
        this.$store.state.widgetView.APIFactor = 1.0 - curVal; // UI.
        this.APIFactor = 1.0 - curVal; 
      },
      APIFactor: function(curVal, oldVal){
        this.$store.state.widgetView.APIFactor = curVal; // API.
        this.$store.state.widgetView.UIFactor = 1.0 - curVal; // API.
        this.UIFactor = 1.0 - curVal; 
      },
      isEdgeAttri: function(curVal, oldVal){
         this.$store.state.widgetView.isEdgeAttri = curVal; // isEdgeAttri.
      },
      probRestart: function(curVal, oldVal){
         this.$store.state.widgetView.probRestart = curVal;  // RWR.      
      },
      weightAttrNode: function(curVal, oldVal){
         this.$store.state.widgetView.weightAttrNode = curVal; // -.
      },
    }

  }
</script>
<style>
 
  .el-collapse-item__header{
    font-size: 18px; /*1.25rem;*/
    /*font-weight: 600;
    padding-left: 15px;*/
  }
  .el-slider{
    width:50%;
  }
  .el-collapse-item__content >div{
    margin:1px;
    /*border:0;*/
    padding:0;
  }
  .block-nodes-control{
     border-style: solid;
     border-color:#ddd;
     border-top-width: 0px;
     border-right-width: 0px;
     border-bottom-width: 0px;
     border-left-width: 0px;
  }
  .block-diffusion{
     border-style: solid;
     border-color:#ddd;
     border-top-width: 0px;
     border-right-width: 0px;
     border-bottom-width: 0px;
     border-left-width: 0px;
  }
  .block-doi{
     border-style: solid;
     border-color:#ddd;
     border-top-width: 0px;
     border-right-width: 0px;
     border-bottom-width: 0px;
     border-left-width: 0px;
  }
  .block-random-walk{
     border-style: solid;
     border-color:#ddd;
     border-top-width: 0px;
     border-right-width: 0px;
     border-bottom-width: 0px;
     border-left-width: 0px;
     margin: 0px 0px 1px 0px; 
  }
  .reset-fault-settings{
    float: right;
    margin:0px 3px 2px 0px;
  }
  #node_num_widget .el-slider__runway {
    width: 100px;
    height: 6px;
    margin: 16px 0;
    background-color: #e4e7ed;
    border-radius: 3px;
    position: relative;
    cursor: pointer;
    vertical-align: middle;
  }

  #node_num_widget .el-collapse-item__header{
    /*background-color: #f5f5f5;*/
    color: #333;  
    border-color:#ddd;
    border-style: solid;
    border-top-width: 1px;
    border-right-width: 1px;
    border-bottom-width: 1px;
    border-left-width: 1px;
    border-top-left-radius:0px;
    border-top-right-radius:0px; 
    border-bottom-left-radius:0px;
    border-bottom-right-radius:0px;
    margin:0px 0px 0px 0px;
  }

  #node_num_widget .el-collapse-item__wrap{
  
  color: #333;  
  border-color:#ddd;
  border-style: solid;
  border-top-width: 0px;
  border-right-width: 1px;
  border-bottom-width: 1px;
  border-left-width: 1px;
  border-top-left-radius:0px;
  border-top-right-radius:0px; 
  border-bottom-left-radius:0px;
  border-bottom-right-radius:0px;
  /*margin:0px 0px 2px 0px;*/
}
#node_num_widget .el-collapse-item__content{
  margin-left: 5px;
}
.settings-block{
  width: 120px;
  display: inline-block;
  margin: 0px 10px 0px 0px;
}
.el-slider__button {
    width: 10px;
    height: 10px;
    border: 2px solid #409EFF;
    background-color: #fff;
    border-radius: 50%;
    -webkit-transition: .2s;
    transition: .2s;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
#node_num_widget .el-button--mini{
    padding-top: 7px;
    padding-right: 4px;
    padding-bottom: 7px;
    padding-left: 4px;
}
#settings-first-box{
  padding: 2px 0px 0px 0px;
}
#node_num_widget .el-slider__bar {
    background-color: #91d5ff;
}
#node_num_widget .el-slider__button {    
    border: 2px solid #91d5ff;   
}
</style>



