<template>
	<div id="conditions-search-view">
	  <el-form :inline="true" ref="searchConditionForm" :model="conditionForm" class="multiple-conditions-form">
        <el-form-item v-loading="loadingFlag" label="">   <!--node-->           
          <el-select class="select-field-input" v-model="selectionfield" filterable placeholder="Select field">
              <el-option
                  v-for="(item, index) in optionsfield"               
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
              </el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-loading="loadingFlag" label="">  <!---->          
          <el-autocomplete
            popper-class="my-autocomplete"
            v-model="keyWord"
            :fetch-suggestions="querySearchAsync"
            placeholder="input"
            @focus="getFocusInInput"
            @select="handleSelect"
          >
          <i class="el-icon-edit el-input__icon"
          slot="suffix">                       
          </i>
          <template slot-scope="{ item }">
            <div class="name">{{ item.value }}</div>              
          </template>
          </el-autocomplete>
        </el-form-item>
        <!-- <el-form-item>
         <img id="icon-add" width="15" height="15" @click="addLadder" src="../../static/img/icon-add.png">
        </el-form-item> -->
        <!---->
       <!-- <el-row style="margin-left:1px;margin-right:-5px;" :gutter="10" v-for="(formObj,formIndex) in conditionForm.formDataList" >         
          
          <el-form-item v-loading="loadingFlag" label=""> 
            <el-select class="select-field-input" v-model="formObj.selectionfield" filterable placeholder="Select field">
                <el-option
                    v-for="(item, index) in optionsfield"               
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                </el-option>
            </el-select>
          </el-form-item>
          
          <el-form-item v-loading="loadingFlag" label=""> 
            <el-autocomplete              
              popper-class="my-autocomplete"
              v-model="formObj.keyWord"            
              :fetch-suggestions="querySearchAsyncMulCons"
              placeholder="input"
              @focus="getFocusInInputNew"
              @select="handleSelect"              
            >
            <i class="el-icon-edit el-input__icon"
            slot="suffix">                       
            </i>
            <template slot-scope="{ item }">
              <div class="name">{{ item.value }}</div>              
            </template>
            </el-autocomplete>
          </el-form-item>       
          
          <el-form-item>            
            <img id="icon-remove" width="15" height="15" @click="deleteLadder(formIndex)" src="../../static/img/icon-subtract.png"> 
          </el-form-item>
       </el-row> -->

       <el-form-item>
          <el-button size="small" type="primary" class="el-icon-search" @click="onSubmit"></el-button>
       </el-form-item>
    
    </el-form>

	</div>
</template>

<script>
  import axios from 'axios' // AJAX
  import * as d3 from '../../static/js/d3.v4.min.js'
  import $ from 'jquery'
  import {vueFlaskRouterConfig, searchFlag} from '../vueFlaskRouterConfig'
  import bus from '../eventbus.js' // .
  
  export default {
  	data() {
      return {
       
        selectionfield: '', // .
        keyWord: "", // .
        
        conditionForm:{ //.
          formDataList: [] //. conditionForm.formDataList:[{selectionfield:"All", keyWord:""}, ...]
        },        
        optionsfield:[], // node.
        allFieldData: [], // ,,:[{value:x, number:x},...]
        allFieldDataEachCon:{}, //{filed1:[{value:x, number:x},...],...}        
        /*
          "name":[{value:"maoty", number:2},{value:"jingjing", number:3}],
          "interests":[{value:"graph drawing", number:2},{value:"information visualization", number:3}]
        */

        loadingDbFlag: false, // .
        loadingFlag: false,  // . true:, false:.
        
        histClickFlag: [false], // .[true],.

        dbLoadedFlag:{}, // .,DOI.
        currentSelectedFieldInMulCons: null, //,. 
      }
  	},
  	watch:{
  		'conditionForm.formDataList':{ //conditionForm.formDataList.
          handler(curVal, oldVal){ //[{selectionfield:"All", keyWord:""}, ...]
            let this_ = this;
            this_.$store.state.conditionsSearch.formDataList = curVal; //[{selectionfield:"All", keyWord:""}, ...]
            let allFieldsSet = new Set(); //.            
            for(let i=0; i<curVal.length; i++){
              let obj = curVal[i];
              allFieldsSet.add(obj.selectionfield); //{xxx, xxx}
            }            
            let exitFieldsList = Object.keys(this_.allFieldDataEachCon);            
            if(allFieldsSet.size > 0){ //.
              allFieldsSet.forEach(function (key, val){ // val:.
                if(val != "All" && val != ""){
                  if(exitFieldsList.indexOf(val) == -1){ //allFieldDataEachCon,.                    
                    let param = {"dbName": this_.$store.state.selection.selectiondb, "field": val}; 
                    axios.post(vueFlaskRouterConfig.infoSearchForDbFieldsValues, {
                      param: JSON.stringify(param)
                    })
                    .then((res) => {
                        let data = res.data; // .
                        this_.allFieldDataEachCon[val] = data; //allFieldDataEachCon,e.g.{name:[{}, ...], }
                        // console.log("this_.allFieldDataEachCon");
                        // console.log(this_.allFieldDataEachCon);                                       
                    })
                    .catch((error) => {            
                      console.error(error);
                    });
                  }
                }

              });
            }
          },
          deep:true
      },

      selectionfield: function(curVal, oldVal){ // selectionfield. ,,.
        let this_ = this;
        this_.$store.state.selection.selectionfield = curVal; // store,.       
        if(this_.$store.state.selection.selectiondb){ // .
          if(this_.selectionfield != "All" && this_.selectionfield != ""){ // ,All.
            this_.allFieldData.splice(0, this_.allFieldData.length);  // . :.
            let param = {"dbName": this_.$store.state.selection.selectiondb, "field": this_.selectionfield}; 
            axios.post(vueFlaskRouterConfig.infoSearchForDbFieldsValues, {
              param: JSON.stringify(param)
            })
            .then((res) => {   
                // this_.loadingFlag = false; // .          
                let data = res.data; // .
                console.log("selectionfield data");
                // this_.allFieldData.splice(0, this_.allFieldData.length);  // . :.  
                this_.allFieldData = data;
                // console.log(data);          
                                
            })
            .catch((error) => {            
              console.error(error);
            }); 
          }
          else{
            this_.allFieldData.splice(0, this_.allFieldData.length);  // . :. 
          }

        }
      },

      keyWord: function(curVal, oldVal){
      	let this_ = this;
        this_.$store.state.selection.keyword = curVal; // store,.        
        if(curVal == ""){  // ,.          
          let isvisible = true; // .
          bus.$emit("tabelDataVisible", isvisible);  // resultsSearch.vue.
        }                     

      }
  	},
  	methods:{
	  deleteLadder(index){
	  	let this_ = this;
	    if(this_.conditionForm.formDataList.length>0){
	       this_.conditionForm.formDataList.splice(index,1); //[{}, {}, ...]	      
	    }
      },
      addLadder(){ //.
      	let this_ = this;
        this_.conditionForm.formDataList.push({selectionfield:"All", keyWord:""}); //[{}, {}, ...]
        
      },
      onSubmit() { //"Search",.
        let this_ = this;
        // bus.$emit("maotingyuntest", true);
        // bus.$emit("sendSplitPanesFlag", true); // .
        if(this_.$store.state.selection.selectiondb != "" && this_.selectionfield != "" && this_.keyWord != ""){ //.
            console.log("Search,!");
            //  +  + .
            let newstate = {
              selectiondb: this_.$store.state.selection.selectiondb,
              selectionfield: this_.selectionfield,
              keyword: this_.keyWord
            }; 
            this_.$store.dispatch('changeStateSelection', newstate); // :this_.$store.dispatch('changeStateSelection', newstate): +  + . 
            let conditionsObj = JSON.parse(JSON.stringify(this_.conditionForm)); //.
            let addConditions = conditionsObj.formDataList; //[{selectionfield:"All", keyWord:""}, ...]
            addConditions.push({selectionfield: this_.selectionfield, keyWord: this_.keyWord}); //.
            // let param = {"dbName": this_.$store.state.selection.selectiondb, "dbfield": this_.selectionfield, "dbfieldvalue": this_.keyWord}; //,.
            let param = {"dbName": this_.$store.state.selection.selectiondb, "conditions": addConditions}; //,.
            axios.post(vueFlaskRouterConfig.infoSearchForResultsSearch, {
              param: JSON.stringify(param)
            })
            .then((res) => {                   
                    // console.log("param..........."); console.log(param);                
                    let tabelData = res.data;  // ,id.                
                    bus.$emit("sendResultsSearch", tabelData);  // resultsSearch.vue.
                              
              })
            .catch((error) => {            
              console.error(error);
            });
        } 

      },
      fuzzyQuery(list, keyWord) { // ,.
        let lowerCasekeyWord = keyWord.toLowerCase(); // .
        var reg =  new RegExp(lowerCasekeyWord);
        var arr = [];
        if(list){
            for (var i = 0; i < list.length; i++) {
              let newStr = list[i].value.toLowerCase(); // .
              if (reg.test(newStr)) {
                arr.push(list[i]);
              }
            }
        }
        
        return arr;
      },      
      querySearchAsync(queryString, cb) {
        let this_ = this;
        // console.log("queryString");console.log(queryString);
        let allFieldData = this_.allFieldData;        
        let results = this_.fuzzyQuery(allFieldData, queryString); // queryString.
        // console.log("results");console.log(results);
        cb(results);         
      },
      querySearchAsyncMulCons(queryString, cb){ //,,.
        let this_ = this;
        let curField = this_.currentSelectedFieldInMulCons;          
        let allFieldData = this_.allFieldDataEachCon[curField]; //.[{value:x, number:x}, ...]        
        let results = this_.fuzzyQuery(allFieldData, queryString); // queryString.        
        cb(results);
      },
      getFocusInInput(event){ //input,.
        // let this_ = this;
        // console.log("event");
        // // console.log(event);
        // let fieldVal = event.path[5]["0"].value; //.
        // console.log(event.path[5]["0"].value);
      },
      getFocusInInputNew(event){
        let this_ = this;        
        let fieldVal = event.path[5].childNodes[1].previousSibling.firstElementChild.children["0"].lastChild.children["0"].value; // todo:,element-ui,div,.
        this_.currentSelectedFieldInMulCons = fieldVal; //.      
      },    
      handleSelect(item) {
        console.log(item);
      }
  	},
  	created(){
  	  let this_ = this;
  	  // bus.$on('sendNodeId', function(data){         
     //    this_.keyWord = String(data); // data String,,idthis_.keyWord.           
     //  });
      bus.$on("restoreFieldKeyWord", function (flag){
         if(flag){
         // 	this_.selectionfield = "All"; 
	        // this_.keyWord = "";
         }
      });
      bus.$on("sendLoadingFlagForFeildKeyWord", function (flag){
      	this_.loadingFlag = flag; ///.
      });
      bus.$on("sendLoadedInitFieldKeyWord", function (fields){ //,+ .
      	  
      	  this_.optionsfield.splice(0,this_.optionsfield.length);  // . :.
	      let allFields = {label:"All", value:"All"};
	      this_.optionsfield.push(allFields);
	      for(let i=0; i<fields.length; i++){
	        this_.optionsfield.push(fields[i]);
	      }                 
	      this_.selectionfield = "All"; // all.
	      this_.allFieldData.splice(0, this_.allFieldData.length);  // . 
	      this_.keyWord = "";
      });
  	},
  	mounted(){    
      let this_ = this;
      console.log("conditionsSearch.vue mounted");
    },    

    beforeDestroy(){      
      // bus.$off("sendNodeId");
      bus.$off("restoreFieldKeyWord");
      bus.$off("sendLoadingFlagForFeildKeyWord");
      bus.$off("sendLoadedInitFieldKeyWord"); //,+ .     
    }
  }

</script>

<style>
.select-field-input .el-input {
  position: relative;
  font-size: 14px;
  display: inline-block;
  width: 150px;
}
#conditions-search-view .el-row{
  margin-left: 1px;margin-right: -5px;
}
#conditions-search-view .el-form-item {
    margin-bottom: 5px;
    margin-top: 2px;
}
#conditions-search-view .select-field-input .el-input {     
    width: 130px;
}
#conditions-search-view .el-button--primary {
    color: #111010;
    background-color: #f5f7fa;
    border-color: #eae9e9;
}
</style>



