<template>
	<div id="focus-node-selection-rank">		
    <!-- <el-collapse v-model="activeNames"> -->
		  <!-- <el-collapse-item title="Ranking List" name="Ranking List">	         -->
		    <div id="focus-node-selection-rank-box">
          <div id="sorted-by-which-method" v-if="tableheader.length > 0">
            <span>Top 100 nodes ranked by</span>          
            <select id="select-method-input" @change="myselect($event)" v-model="rankingMethod">
              <option :value="item.value" v-for="item in methodOptions">{{item.value}}</option>            
            </select> 
          </div>
          <div id="ranking-by-node-measures" v-if="tableheader.length > 0">
              <el-table            
              :data="tableData"
              border
              ref="resultTable"
              highlight-current-row
              :row-class-name="tableRowClassNameResult"
              :cell-class-name="tableCellClassNameResult"
              empty-text="null"            
              style="width: 100%"    
              :max-height="tableheight"
              @cell-click="cellClickEvent"
              >
                <el-table-column
                  type="index"
                  fixed
                  width="50" >   
                </el-table-column>       
                <el-table-column
                  v-for="item in tableheader"
                  v-if="item.prop!='id'"      
                  :prop= "item.prop"
                  :label= "item.label"
                  :width="item.width"                      
                  show-overflow-tooltip
                  >
                </el-table-column>      
              </el-table>            
          </div>
        </div>
        
		  <!-- </el-collapse-item> -->
		<!-- </el-collapse>     -->
  <div id="ranking-table-see-more"></div> <!--,-->
  <div id="interest-attributes-ranking-list">
      <focusattributesselection :callback="callbackForInterestAttributes"></focusattributesselection>
  </div>
	</div>	
</template>

<script>
 
  import axios from 'axios' // AJAX
  import * as d3 from '../../static/js/d3.v4.min.js'
  // import {d3GraphLayout} from "../../static/js/d3ForceGraph.js"
  import {jBox} from "../../static/js/jBox.js" 
  import $ from 'jquery'
  import {vueFlaskRouterConfig} from '../vueFlaskRouterConfig'
  import bus from '../eventbus.js' // .
  import "../../static/js/jquery.contextMenu.js"
  import "../../static/js/jquery.ui.position.js"
  import focusattributesselection from "@/components/focusAttributesSelection"

  export default {    
    data: function(){
      return {
         tableheader: [], // 
         tableData: [], // .
         tableheight: "250", // 
         activeNames:["Ranking List"],
         ableClickClassName:"clickSeeMore",
         // whichMethod: "API", //API.
         rankingMethod: "API",
         methodOptions:[
           {value: 'API'},           
           {value: 'Degree'},          
           {value: 'Betweenness'},
           {value: 'Closeness'}
         ],
         seeMoreFieldList: null, //{"publication",...}
         seeMoreObjList: {}, // {}
         jBoxInstance: {
          seeMoreJbox: null, //see more box.
          interestAttributes: null, // .
         },
         rightClickNodeGetId: "", // id.

      }
    },
    computed:{
       
    },
    components: { // .
       focusattributesselection
    },
    created(){
       let this_ = this;
       bus.$on('sendRankTableNodeMeasure', function(data){          
           this_.tableheader = data.tableheader; // 
           if(this_.$store.state.selection.selectiondb == "co-authorship.db"){
               let tableData = data.tabledata; // 
               let indexTheMan = null;

               for(let i=0; i<tableData.length; i++){
                 let objId = tableData[i].id;
                 if(objId == "647629"){
                   indexTheMan = i;
                   break;
                 }             
               }

               let newTableData = this_.swapItem(tableData, indexTheMan, 4);
               this_.tableData = newTableData; // 
           }
           else{
             this_.tableData = data.tabledata; // 
           }
           
          
           this_.seeMoreObjList = {}; //.
           this_.seeMoreFieldList = new Set(); //.
           for(let i=0; i<data.tabledata.length; i++){
             let row = data.tabledata[i]; //,row={id:x, name:x, ..., publications:x}
             let keyListRow = Object.keys(row); //[id, name, ...]
             let tempObj = {}; //{publications:x}
             let id = '';
             keyListRow.forEach(function(key, index){
               if(key == "id"){
                 id = row[key];
               }
               if(this_.$store.state.infoSearchView.dbtables.indexOf(key) != -1){ //.
                  tempObj[key] = row[key];
                  this_.seeMoreFieldList.add(key);//{publications,...}
               }
             });
             this_.seeMoreObjList[id] = tempObj;
           }
       });
       bus.$on("sendRankingDefaultMethod", function(data){ // data,e.g., "Citation_Visualization.db"
         if(data){
           this_.rankingMethod = "API";
           this_.tableData.splice(0, this_.tableData.length);
           this_.tableheader.splice(0, this_.tableheader.length);
           this_.$store.state.selection.dboptions.forEach(function (item, index){               
               if(item.label == data){
                  let graphDirected = item.directed; // true/false
                  if(graphDirected){ // true
                    this_.methodOptions = [
                       {value: 'API'},
                       {value: 'Degree'},
                       {value: 'In-Degree'},
                       {value: 'Out-Degree'},          
                       {value: 'Betweenness'},
                       {value: 'Closeness'}
                    ];
                  }
                  else{ // false
                    this_.methodOptions = [
                       {value: 'API'},
                       {value: 'Degree'},                                 
                       {value: 'Betweenness'},
                       {value: 'Closeness'}
                    ];
                  }
               }
           });
         }
       });
       // bus.$on("snedDboptions", function(data){
          
       // });
       
    },
    methods:{
      swapItem(arr, fromIndex, toIndex) {
            arr[toIndex] = arr.splice(fromIndex, 1, arr[toIndex])[0];
            return arr;
      },
      callbackForInterestAttributes(){
          let this_ = this;
          let nodeId = this_.rightClickNodeGetId;
          let row = null; // ,.
          for(let i=0; i<this_.tableData.length; i++){ // .
             let obj = this_.tableData[i];
             if(obj.id == nodeId){ // id,.
               row = obj;
               break; // ,.,,.
             }
          }
          
          let id = row.id; // id. string.
          let tag = row.name; // . 
          let dbname = this_.$store.state.selection.selectiondb; // .
          let field =  this_.$store.state.selection.selectionfield; // .
          let keyword = this_.$store.state.selection.keyword; // .         
          // let fieldString = field;
          // let keywordString = keyword;
          // let consObjList = this_.$store.state.conditionsSearch.formDataList; //[{selectionfield:"All", keyWord:""}, ...]
          // for(let i=0; i<consObjList.length; i++){
          //   let obj = consObjList[i];
          //   let field = obj.selectionfield;
          //   let keyWord = obj.keyWord;
          //   fieldString += ";" + field;
          //   keywordString += ";" + keyWord;
          // }
          let focusObj = {"id": id,  // id,
                          "tag": tag, // tag.
                          "dbname":dbname,
                          "field":"NULL",
                          "keyword": "NULL",
                          "attriselect": this_.$store.state.focusAttributesSelection.checkedFocusAttri
                          }; 
          
          let attributesValue = {};
          let keysRow = Object.keys(row); // . [field1, ...]         
          let keysType = this_.$store.state.fields.fieldsType; // .{:,...}
          // console.log("keysType");console.log(keysType);
          for(let i=0; i<keysRow.length; i++){
             let key_ = keysRow[i]; // 
             let type_ = keysType[key_]; // 
             let computedAttri = this_.$store.state.focusAttributesSelection.checkedFocusAttri;
             if(computedAttri.indexOf(key_) != -1){ // .
               attributesValue[key_] = row[key_];
             }
          }        
          focusObj.attributesValue = attributesValue; // .         
          bus.$emit('sendSlectedNode', focusObj); //sendSlectedNode,focusSetView.vue.
          $("#ranking-by-node-measures ." + nodeId + " td:first-child").css("background-color", this_.$store.state.resultsSearchView.focusColor); //,.
          // if(this_.$store.state.resultsSearchView.isOpenResultVis){ //result vis .
          //   $("#" + this_.$store.state.resultsSearchView.svgId + " ." + nodeId).css("fill", this_.$store.state.resultsSearchView.focusColor); //,,.
          // }
          // this_.$store.state.resultsSearchView.focusStateList.push(nodeId); 
          this_.$store.state.nodeRankTable.focusStateList.push(nodeId); // //id.
          this_.jBoxInstance.interestAttributes.close();
      },
      sprintf(){  // .
        let args = arguments,
        string = args[0],
        i = 1;
        return string.replace(/%((%)|s|d)/g, function (m) {
            // m is the matched format, e.g. %s, %d
            let val = null;
            if (m[2]) {
                val = m[2];
            } else {
                val = args[i];
                // A switch statement so that the formatter can be extended. Default is %s
                switch (m) {
                    case '%d':
                        val = parseFloat(val);
                        if (isNaN(val)) {
                            val = 0;
                        }
                        break;
                }
                i++;
            }
            return val;
        });
      },
      indexOfMax(arr) {
          if (arr.length === 0) {
              return -1;
          }

          var max = arr[0];
          var maxIndex = 0;

          for (var i = 1; i < arr.length; i++) {
              if (arr[i] > max) {
                  maxIndex = i;
                  max = arr[i];
              }
          }

          return maxIndex;
      },
      orderForArray(yearArr, idArr){ //see more.
         let orderIdArr = [];
         let orderYear = [];

         let newYearArr = yearArr.slice();

         for(let i=0; i<newYearArr.length; i++){
            let maxIndex = this.indexOfMax(newYearArr);
            let maxVal = newYearArr[maxIndex];
            newYearArr[maxIndex] = 0;

            orderYear.push(maxVal);
            orderIdArr.push(idArr[maxIndex]);
         }
         return {orderIdArr: orderIdArr, orderYear: orderYear};
      },
      cellClickEvent(row, column, cell, event){ //:+,.
        let this_ = this;
        let key = column.label; //publications.
        if(this_.$store.state.infoSearchView.dbtables.indexOf(key) != -1){ //."see more...".
          this_.isClickSeeMoreOut = true;
          let nodeId = '';
          let letterCalssList = ["context-menu-active", "current-row", "el-table__row", "hover-row"];//,..                
          event.target.offsetParent.parentElement.classList.forEach(function(item, index){
            if(letterCalssList.indexOf(item) == -1){ //.
               nodeId = item;
            }
          });
          // if(this_.tableRowClickTempNodeList.length > 0){
          //   this_.tableRowClickTempNodeList.pop(); //.
          //   this_.selectedTempRowList.pop();
          // }
          // if(this_.tableRowClickNodeList.length > 0){ //
          //   let id = this_.tableRowClickNodeList[0]; //.
          //   if(nodeId == id){ //.
          //     this_.isClickSeeMore = true; // see more
          //     this_.tableRowClickTempNodeList.push(id); //id.
          //     this_.selectedTempRowList.push(this_.selectedRowList[0]); //row.
          //   }
          // }
          console.log("nodeId");console.log(nodeId);
          console.log("key");console.log(key);
          console.log("this_.seeMoreObjList");console.log(this_.seeMoreObjList);
          
          let obj = this_.seeMoreObjList[nodeId]; //{publications:xxxx, ...}
          let value = obj[key]; // value="id1;id2;id3;id4",id.
        
          let dbName = this_.$store.state.selection.selectiondb; // .
          let tableName = key;
          let param = { "dbName": dbName, // DB name
                        "value": value, // value="id1;id2;id3;id4"
                        "tableName": tableName}; // tableName="publications",.
          
          axios.post(vueFlaskRouterConfig.mainViewNodeDetailsSeeMore, {
              param: JSON.stringify(param)
          })
          .then((res) => { 
              let data = res.data;  // ,id.
              if(tableName == "publications"){ // todo: publications,..
                // this_.jboxTitle = "Publication";
                let allIdsList = Object.keys(data); // [id1, id2, ...], {id:{}, ...}
                
                let yearList = []; // [2016, 2018, ...] int.
                for(let i=0; i<allIdsList.length; i++){
                  let id = allIdsList[i];
                  let eachObj = data[id];
                  yearList.push(parseInt(eachObj.year));
                }                
                let orderObj = this_.orderForArray(yearList, allIdsList); // {orderIdArr: orderIdArr, orderYear: orderYear}              
                let orderId = orderObj.orderIdArr;                
                let totalNum = allIdsList.length; // .
                let tempHtml = "<div class='author-number'><span>Author: %s</span><span>&nbsp&nbsp&nbsp&nbsp&nbsp&nbspnumber: %s</span></div>"; 
                let allHtml = this_.sprintf(tempHtml, row.name, String(totalNum));     
                for(let i=0; i<orderId.length; i++){
                  let id = orderId[i];
                  let obj = data[id];
                  let title = obj.title;
                  let authors = obj.authors;
                  let public_venue = obj.public_venue;
                  let year = obj.year;
                  let abstract = obj.abstract;
                  let html = '<div class="each-one"><h5 class="content">%s. %s</h5><p class="content"><em id="author">%s</em></p><p class="content"><strong id="venue">%s(%s)</strong></p><span class="collapsibleinseemore"><img src="data:image/gif;base64,"><span class="collapsible-icon-name">Abstract</span></span><div class="collapsible-content"><div class="abstract"><div class="abstract-content">%s</div></div></div></div>';
                  let newHtml = this_.sprintf(html, String(i+1), title, authors, public_venue, year, abstract);
                  allHtml += newHtml;
                }
                $("#ranking-table-see-more").html(allHtml); // updated.,initSeeMore.
                this_.initSeeMore();
              }          
              
            })
            .catch((error) => {            
              console.error(error);
            });

        }
        else{ // see more.
          this_.jBoxInstance.seeMoreJbox.close(); //. .
        }
      },
      initSeeMore(){
        let coll = $(".collapsibleinseemore");    
        for(let i=0; i<coll.length; i++){
          coll[i].addEventListener("click", function() {
            this.classList.toggle("active");
            let content = this.nextElementSibling;
            if (content.style.display === "block") {
              content.style.display = "none";
            } else {
              content.style.display = "block";
            }
          });
        }
      },
      insertSeeMore(){
        let this_ = this;        
        let keyList = Array.from(this_.seeMoreFieldList); //.        
        for(let i=0; i<keyList.length; i++){
          let className = keyList[i]; // ,calss name.
          let html = "<p class='cell-see-more'>See More...</p>";
          $("#ranking-by-node-measures ." + className + " .cell").html(html);
        } 
        
      },
      myselect(event) {
        let this_ = this;
        let whichMethod = event.target.value; //     
        if(this_.$store.state.isUsingRankMeasures){ // .
            let requestNodeRankTable = vueFlaskRouterConfig.selctionFocusNodesByNodeMeasures + "/" + this_.$store.state.selection.selectiondb + "/" + whichMethod + "/" + this_.$store.state.nodeRankTable.topN;
            axios.get(requestNodeRankTable)
            .then((res) => {
              let nodeRankTable = res.data;
              this_.tableheader = nodeRankTable.tableheader; // 
              this_.tableData = nodeRankTable.tabledata;
            })
            .catch((error) => {            
              console.error(error);
            });
        }
      },
      handleCurrentChange(currentPage) { //.
        let this_ = this;
        this_.currentPage = currentPage;
      },
      tableCellClassNameResult({row, column, rowIndex, columnIndex}){ //class.
        let this_ = this;        
        let fieldName = column.label; //.
        let className  = this_.ableClickClassName + " " + fieldName; // .
        if(this_.$store.state.infoSearchView.dbtables.indexOf(fieldName) != -1){ // .
           return "" + className;
        }
        else{
          return "" + fieldName;
        }
      },
      tableRowClassNameResult({row, rowIndex}){ //,/,,.
        let this_ = this;
        let rowClass = row.id; //id.        
        return rowClass;        
      },

    },
    watch:{
      // whichMethod: function(curVal, oldVal){
      //   console.log("whichMethod: function(curVal, oldVal)");
      //   console.log(curVal);
      // }
    },
    mounted(){
      let this_ = this;
      $.contextMenu({  // fixme:,.
            selector: '#ranking-by-node-measures .el-table__row',
            className: "focusSelectionMenuRanking", // .  
            callback: function(key, options) {
                 let nodeId = '';
                 let letterCalssList = ["context-menu-active", "current-row", "el-table__row"];//.               
                  options.$trigger.context.classList.forEach(function(item, index){
                    if(letterCalssList.indexOf(item) == -1){ //.
                       nodeId = item;
                    }
                  });
                 
                 if(key == "copy"){  // ,.
                    this_.rightClickNodeGetId = nodeId;
                    
                  }
                 if(key == "cut"){ // .
                   bus.$emit("sendDeleteFocusInRankingList", nodeId);                   
                   this_.$store.state.nodeRankTable.focusStateList.splice(this_.$store.state.nodeRankTable.focusStateList.indexOf(nodeId), 1); //nodeId.                 
                 }
            },
            items: { // todo:.
                "copy": {name: "Add it to the focus set", className: "addToTheFocusSet"}, // . , icon: "copy" 
                "cut": {name: "Delete it from the focus set"},              
                "quit": {name: "Quit"}                
            }                   
      });
      this_.jBoxInstance.interestAttributes = new jBox('Modal', {
            id: "jBoxInterestAttributesRanking", // .
            addClass: "jBoxInterestAttributesRankingClass",  // ,!
            attach: '.focusSelectionMenuRanking .addToTheFocusSet',
            width: 300,              // Maximal width
            height: 150,             // Maximal height 
            title: 'Attributes of Interest',
            // fixed:true,
            overlay: false,
            fixed: false,
            adjustTracker: true,
            zIndex: 1005, // fixme:jboxzIndex.
            createOnInit: true,
            content: $("#interest-attributes-ranking-list"),  // jQuery('#jBox-content') 
            draggable: true,
            repositionOnOpen: false,
            repositionOnContent: true,
            target: $('.focusSelectionMenuRanking .addToTheFocusSet'),//$('#filtered-subgraph-nav-icons-setting'),
            offset: {x: -135, y: 100}, // {x: -135, y: 155},
            // ,!
            onOpen: function(){                   
                this_.jBoxInstance.interestAttributes.position({
                   target: $('.focusSelectionMenuRanking .addToTheFocusSet')//$('#filtered-subgraph-nav-icons-setting') //,: jBoxInstance.resultVis ,layoutSetting,layoutSettingjBoxInstance.resultVis.           
                });
                bus.$emit("attributesOfInterestPanelUpdate", true); //  "" .
            },
            onCloseComplete: function(){                     
              
            }
      });
      
    },
    updated(){
      let this_ = this;
      console.log("globalGraphView updated maotingyun");
      if(this_.tableheader.length > 0){
        this_.insertSeeMore();
        this_.jBoxInstance.seeMoreJbox = new jBox('Modal', {
                  //todo: id.
                  addClass: "jBoxSeeMoreOfNodeRanking",  // ,!
                  attach: "#focus-node-selection-rank ." + this_.ableClickClassName, //#resultssearch,.
                  width: 400,              // Maximal width 
                  maxHeight: 800,              
                  title: "Publications",
                  overlay: false,
                  zIndex: 1005, // fixme:jboxzIndex.
                  createOnInit: true,
                  content: $("#ranking-table-see-more"),  //,.
                  draggable: true,
                  repositionOnOpen: false,
                  repositionOnContent: true,                  
                  position:this_.$store.state.jboxPositionSet.topRightSide,
                  // ,!
                  onOpen: function(){
                  
                  },
                  onCloseComplete: function(){ // todo: : onCloseComplete.see more,mainViewjquerycssdiaplay == block none.
                  }
         });
        if($(".jBoxSeeMoreOfNodeRanking").length > 1){ // 
            $(".jBoxSeeMoreOfNodeRanking").first().remove(); // .first()
        }
      }
      
    },
    beforeDestroy(){
      bus.$off('sendRankTableNodeMeasure'); // .
      bus.$off("sendRankingDefaultMethod");
      // bus.$off("snedDboptions");
    }   
  }
</script>

<style>
#ranking-table-see-more {
    display: none;
    max-height: 400px; 
    max-width: 500px;
    padding:2px 2px 2px 4px;
    overflow: scroll;
  }
  
/*  */
#ranking-table-see-more .abstract {
  max-height: 217px;
  max-width: 489px;
  overflow: scroll;
  padding: 0px 0px 0px 5px;  
  margin: 5px 1px 5px 1px;
  font: normal normal normal normal 12px / 20px Lato, sans-serif;  
}
#ranking-table-see-more .author-number{
  margin:2px 0px 8px 0px;
  border-style: solid;
  border-color:#E0E0E0;
  border-top-width: 0px;
  border-right-width: 0px;
  border-bottom-width: 1px;
  border-left-width: 0px;
  font-size: 12px;
}
#ranking-table-see-more .author-number span{
  width:100%;
  border-spacing: 2px 2px;
  font: normal normal normal normal 12px;
  font-weight: 600;
  
}
#ranking-table-see-more #author{
    color: #069;
    border-spacing: 2px 2px;
    font: italic normal 300 normal 12px / 20px Lato, sans-serif;
}
#ranking-table-see-more #venue{
    border-spacing: 2px 2px;
    font: normal normal normal normal 12px / 20px Lato, sans-serif;
}
#ranking-table-see-more h5{
   color: #069;
}
#ranking-table-see-more .content{
  width: 489px;
  margin: 1px 1px 1px 1px;

}
#info-paper-box{
  max-height: 700px;
  max-width: 500px;
  overflow: scroll;
}

#ranking-table-see-more .collapsible {
  color: #444;
  cursor: pointer;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}

#ranking-table-see-more .active .collapsible-icon-name, #ranking-table-see-more .collapsible .collapsible-icon-name:hover {
  background-color: #069;

}
.collapsibleinseemore {
  font-size:12px;
}
.collapsibleinseemore img {
  height: 12px;
}

#ranking-table-see-more .collapsible-content {
  display: none;
  overflow: hidden;
  background-color: #f1f1f1;
}
#ranking-table-see-more .each-one{
 border-style: solid;
 border-color:#E0E0E0;
 border-top-width: 0px;
 border-right-width: 0px;
 border-bottom-width: 1px;
 border-left-width: 0px;
}

</style>


